import { useState, useEffect } from 'react';
import { 
  BarChart3, Brain, Target, TrendingUp, Clock, 
  CheckCircle, BookOpen, Zap
} from 'lucide-react';
import { db, getStudyStats } from '../db';
import type { QuizResult } from '../types';

interface ProgressDashboardProps {
  bookId?: string;
}

export default function ProgressDashboard({ bookId }: ProgressDashboardProps) {
  const [stats, setStats] = useState({
    totalFlashcards: 0,
    dueFlashcards: 0,
    totalQuestions: 0,
    averageScore: 0,
    studyStreak: 0,
    conceptsMastered: 0,
    totalConcepts: 0,
  });
  const [quizResults, setQuizResults] = useState<QuizResult[]>([]);
  const [isLoading, setIsLoading] = useState(true);

  useEffect(() => {
    loadData();
  }, [bookId]);

  const loadData = async () => {
    setIsLoading(true);
    
    const [studyStats, results] = await Promise.all([
      getStudyStats(bookId),
      db.quizResults.orderBy('date').reverse().limit(10).toArray(),
    ]);
    
    setStats(studyStats);
    setQuizResults(results);
    setIsLoading(false);
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="glass rounded-2xl p-8">
          <BarChart3 className="w-8 h-8 text-violet-400 animate-pulse" />
        </div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="glass rounded-2xl p-6">
        <div className="flex items-center gap-4">
          <div className="w-14 h-14 rounded-2xl bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center">
            <Target className="w-7 h-7 text-white" />
          </div>
          <div>
            <h2 className="text-2xl font-bold text-white">Your Progress</h2>
            <p className="text-slate-400">Track your learning journey</p>
          </div>
        </div>
      </div>

      {/* Stats Grid */}
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
        {[
          { label: 'Flashcards', value: stats.totalFlashcards, icon: Brain, color: 'violet' },
          { label: 'Due Today', value: stats.dueFlashcards, icon: Clock, color: 'amber' },
          { label: 'Quiz Avg', value: `${stats.averageScore}%`, icon: TrendingUp, color: 'emerald' },
          { label: 'Streak', value: `${stats.studyStreak} days`, icon: Zap, color: 'fuchsia' },
        ].map((stat) => (
          <div key={stat.label} className="glass rounded-xl p-4">
            <div className="flex items-center gap-2 mb-2">
              <stat.icon className={`w-4 h-4 text-${stat.color}-400`} />
              <span className="text-slate-500 text-sm">{stat.label}</span>
            </div>
            <p className={`text-2xl font-bold text-${stat.color}-400`}>{stat.value}</p>
          </div>
        ))}
      </div>

      {/* Concept Mastery */}
      <div className="glass rounded-2xl p-6">
        <div className="flex items-center justify-between mb-4">
          <h3 className="text-lg font-semibold text-white">Concept Mastery</h3>
          <span className="text-sm text-slate-400">
            {stats.conceptsMastered}/{stats.totalConcepts} mastered
          </span>
        </div>
        
        <div className="glass rounded-full h-3 overflow-hidden mb-4">
          <div 
            className="h-full bg-gradient-to-r from-violet-500 to-fuchsia-500 transition-all"
            style={{ width: `${stats.totalConcepts > 0 ? (stats.conceptsMastered / stats.totalConcepts) * 100 : 0}%` }}
          />
        </div>
        
        {stats.conceptsMastered === 0 && stats.totalConcepts === 0 ? (
          <div className="text-center py-8">
            <BookOpen className="w-12 h-12 mx-auto mb-3 text-slate-500" />
            <p className="text-slate-400">Upload study material to track concept mastery</p>
          </div>
        ) : (
          <div className="grid grid-cols-3 gap-4">
            <div className="text-center">
              <p className="text-2xl font-bold text-emerald-400">{stats.conceptsMastered}</p>
              <p className="text-xs text-slate-500">Mastered</p>
            </div>
            <div className="text-center">
              <p className="text-2xl font-bold text-yellow-400">{Math.max(0, stats.totalConcepts - stats.conceptsMastered)}</p>
              <p className="text-xs text-slate-500">Learning</p>
            </div>
            <div className="text-center">
              <p className="text-2xl font-bold text-violet-400">{stats.totalQuestions}</p>
              <p className="text-xs text-slate-500">Questions</p>
            </div>
          </div>
        )}
      </div>

      {/* Recent Quiz Results */}
      <div className="glass rounded-2xl p-6">
        <h3 className="text-lg font-semibold text-white mb-4">Recent Quiz Results</h3>
        
        {quizResults.length === 0 ? (
          <div className="text-center py-8">
            <CheckCircle className="w-12 h-12 mx-auto mb-3 text-slate-500" />
            <p className="text-slate-400">No quizzes taken yet. Start practicing!</p>
          </div>
        ) : (
          <div className="space-y-3">
            {quizResults.map((result) => (
              <div key={result.id} className="flex items-center justify-between p-4 glass rounded-xl">
                <div className="flex items-center gap-3">
                  <div className={`w-10 h-10 rounded-xl flex items-center justify-center ${
                    result.score >= 80 ? 'bg-emerald-500/20' :
                    result.score >= 60 ? 'bg-yellow-500/20' :
                    'bg-red-500/20'
                  }`}>
                    <CheckCircle className={`w-5 h-5 ${
                      result.score >= 80 ? 'text-emerald-400' :
                      result.score >= 60 ? 'text-yellow-400' :
                      'text-red-400'
                    }`} />
                  </div>
                  <div>
                    <p className="font-medium text-white">Quiz on {new Date(result.date).toLocaleDateString()}</p>
                    <p className="text-sm text-slate-400">{result.correctAnswers}/{result.totalQuestions} correct  {Math.floor(result.timeSpent / 60)}m {result.timeSpent % 60}s</p>
                  </div>
                </div>
                <span className={`text-lg font-bold ${
                  result.score >= 80 ? 'text-emerald-400' :
                  result.score >= 60 ? 'text-yellow-400' :
                  'text-red-400'
                }`}>
                  {result.score}%
                </span>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
}
