import Dexie, { Table } from 'dexie';
import type {
  Book,
  Chapter,
  Flashcard,
  Question,
  StudySession,
  StudySchedule,
  Progress,
  QuizResult,
  Concept,
  ConceptMap,
} from '../types';

export class StudyDatabase extends Dexie {
  books!: Table<Book>;
  chapters!: Table<Chapter>;
  flashcards!: Table<Flashcard>;
  questions!: Table<Question>;
  studySessions!: Table<StudySession>;
  studySchedules!: Table<StudySchedule>;
  progress!: Table<Progress>;
  quizResults!: Table<QuizResult>;
  concepts!: Table<Concept>;
  conceptMaps!: Table<ConceptMap>;

  constructor() {
    super('RealEstateStudyDB');
    
    this.version(2).stores({
      books: 'id, title, uploadedAt',
      chapters: 'id, bookId, number, title, createdAt, *topics',
      flashcards: 'id, chapterId, conceptId, nextReviewDate, difficulty, *timesReviewed',
      questions: 'id, chapterId, conceptId, difficulty, topic, *tags',
      studySessions: 'id, date, type, *conceptsReviewed',
      studySchedules: 'id, examDate',
      progress: 'id, bookId, lastStudied, *conceptsMastered',
      quizResults: 'id, date, chapterId, score, *weakAreas',
      concepts: 'id, name, difficulty, *tags, *relatedConcepts',
      conceptMaps: 'id, chapterId',
    });
  }
}

export const db = new StudyDatabase();

// ============================================================================
// FLASHCARD HELPERS
// ============================================================================

export async function getDueFlashcards(): Promise<Flashcard[]> {
  const now = new Date();
  return await db.flashcards
    .where('nextReviewDate')
    .belowOrEqual(now)
    .toArray();
}

export async function getFlashcardsByChapter(chapterId: string): Promise<Flashcard[]> {
  return await db.flashcards.where('chapterId').equals(chapterId).toArray();
}

export async function getFlashcardsByConcept(conceptId: string): Promise<Flashcard[]> {
  return await db.flashcards.where('conceptId').equals(conceptId).toArray();
}

// SM-2 Algorithm for spaced repetition
export async function updateFlashcardAfterReview(
  flashcardId: string,
  quality: number // 0-5 rating
): Promise<void> {
  const flashcard = await db.flashcards.get(flashcardId);
  if (!flashcard) return;

  let { interval, repetition, easinessFactor, timesReviewed, timesCorrect } = flashcard;

  // Update stats
  timesReviewed++;
  if (quality >= 3) timesCorrect++;

  // Update easiness factor
  easinessFactor = Math.max(
    1.3,
    easinessFactor + (0.1 - (5 - quality) * (0.08 + (5 - quality) * 0.02))
  );

  if (quality < 3) {
    repetition = 0;
    interval = 1;
  } else {
    repetition += 1;
    if (repetition === 1) {
      interval = 1;
    } else if (repetition === 2) {
      interval = 6;
    } else {
      interval = Math.round(interval * easinessFactor);
    }
  }

  const nextReviewDate = new Date();
  nextReviewDate.setDate(nextReviewDate.getDate() + interval);

  await db.flashcards.update(flashcardId, {
    interval,
    repetition,
    easinessFactor,
    nextReviewDate,
    lastReviewedDate: new Date(),
    timesReviewed,
    timesCorrect,
  });
}

// ============================================================================
// QUESTION HELPERS
// ============================================================================

export async function getQuestionsByChapter(chapterId: string): Promise<Question[]> {
  return await db.questions.where('chapterId').equals(chapterId).toArray();
}

export async function getQuestionsByConcept(conceptId: string): Promise<Question[]> {
  return await db.questions.where('conceptId').equals(conceptId).toArray();
}

export async function getQuestionsByDifficulty(difficulty: 'easy' | 'medium' | 'hard'): Promise<Question[]> {
  return await db.questions.where('difficulty').equals(difficulty).toArray();
}

// ============================================================================
// CONCEPT HELPERS
// ============================================================================

export async function getConceptsByChapter(chapterId: string): Promise<Concept[]> {
  const chapter = await db.chapters.get(chapterId);
  if (!chapter || !chapter.concepts) return [];
  
  const conceptIds = chapter.concepts.map(c => c.id);
  return await db.concepts.where('id').anyOf(conceptIds).toArray();
}

export async function getRelatedConcepts(conceptId: string): Promise<Concept[]> {
  const concept = await db.concepts.get(conceptId);
  if (!concept) return [];
  
  return await db.concepts.where('id').anyOf(concept.relatedConcepts).toArray();
}

export async function updateConceptMastery(
  conceptId: string,
  mastery: number
): Promise<void> {
  const concept = await db.concepts.get(conceptId);
  if (!concept) return;

  // Update concept mastery in progress records
  const progressRecords = await db.progress.toArray();
  
  for (const progress of progressRecords) {
    const existingIndex = progress.conceptProgress?.findIndex(
      cp => cp.conceptId === conceptId
    ) ?? -1;

    if (existingIndex >= 0) {
      progress.conceptProgress[existingIndex].mastery = mastery;
      progress.conceptProgress[existingIndex].lastReviewed = new Date();
    } else {
      progress.conceptProgress = progress.conceptProgress || [];
      progress.conceptProgress.push({
        conceptId,
        mastery,
        lastReviewed: new Date(),
      });
    }

    await db.progress.update(progress.id, {
      conceptProgress: progress.conceptProgress,
    });
  }
}

// ============================================================================
// PROGRESS HELPERS
// ============================================================================

export async function getProgressByBook(bookId: string): Promise<Progress | undefined> {
  return await db.progress.where('bookId').equals(bookId).first();
}

export async function getWeakTopics(bookId: string): Promise<string[]> {
  const quizResults = await db.quizResults.toArray();
  const topicPerformance: Record<string, { correct: number; total: number }> = {};

  for (const result of quizResults) {
    const chapter = await db.chapters.get(result.chapterId);
    if (!chapter || chapter.bookId !== bookId) continue;

    const topic = chapter.title;
    if (!topicPerformance[topic]) {
      topicPerformance[topic] = { correct: 0, total: 0 };
    }
    topicPerformance[topic].correct += result.correctAnswers;
    topicPerformance[topic].total += result.totalQuestions;
  }

  const weakTopics: string[] = [];
  for (const [topic, performance] of Object.entries(topicPerformance)) {
    const accuracy = performance.total > 0 ? performance.correct / performance.total : 0;
    if (accuracy < 0.7) {
      weakTopics.push(topic);
    }
  }

  return weakTopics;
}

export async function getWeakConcepts(bookId: string): Promise<Concept[]> {
  const progress = await getProgressByBook(bookId);
  if (!progress || !progress.conceptProgress) return [];

  const weakConceptIds = progress.conceptProgress
    .filter(cp => cp.mastery < 70)
    .map(cp => cp.conceptId);

  return await db.concepts.where('id').anyOf(weakConceptIds).toArray();
}

export async function recordStudySession(
  session: Omit<StudySession, 'id'>
): Promise<void> {
  await db.studySessions.add({
    ...session,
    id: crypto.randomUUID(),
  });
}

// ============================================================================
// STATS HELPERS
// ============================================================================

export async function getStudyStats(bookId?: string): Promise<{
  totalFlashcards: number;
  dueFlashcards: number;
  totalQuestions: number;
  averageScore: number;
  studyStreak: number;
  conceptsMastered: number;
  totalConcepts: number;
}> {
  const allFlashcards = await db.flashcards.toArray();
  const now = new Date();
  const dueCards = allFlashcards.filter(f => new Date(f.nextReviewDate) <= now);
  
  const allQuestions = await db.questions.toArray();
  const quizResults = await db.quizResults.toArray();
  
  const avgScore = quizResults.length > 0
    ? Math.round(quizResults.reduce((sum, r) => sum + r.score, 0) / quizResults.length)
    : 0;

  // Calculate study streak
  const uniqueDates = [...new Set(quizResults.map(r => new Date(r.date).toDateString()))];
  let streak = 0;
  const today = new Date().toDateString();
  const yesterday = new Date(Date.now() - 86400000).toDateString();
  
  if (uniqueDates.includes(today) || uniqueDates.includes(yesterday)) {
    streak = uniqueDates.length;
  }

  // Get concept stats
  let conceptsMastered = 0;
  let totalConcepts = 0;
  
  if (bookId) {
    const progress = await getProgressByBook(bookId);
    if (progress?.conceptProgress) {
      conceptsMastered = progress.conceptProgress.filter(cp => cp.mastery >= 80).length;
    }
    const book = await db.books.get(bookId);
    totalConcepts = book?.totalConcepts || 0;
  }

  return {
    totalFlashcards: allFlashcards.length,
    dueFlashcards: dueCards.length,
    totalQuestions: allQuestions.length,
    averageScore: avgScore,
    studyStreak: streak,
    conceptsMastered,
    totalConcepts,
  };
}

// ============================================================================
// DATA MIGRATION
// ============================================================================

export async function migrateLegacyData(): Promise<void> {
  // Check if migration is needed
  const legacyChapters = await db.chapters.toArray();
  
  for (const chapter of legacyChapters) {
    // Migrate old chapters to new format if needed
    if (!chapter.eli5Summary && chapter.summary) {
      await db.chapters.update(chapter.id, {
        eli5Summary: {
          oneSentence: chapter.summary.substring(0, 100) + '...',
          shortSummary: chapter.summary,
          whyItMatters: 'Understanding this material is essential for your real estate exam.',
          realWorldConnection: 'This knowledge will help you serve your clients effectively.',
        },
        concepts: (chapter.keyTerms || []).map((kt, i) => ({
          id: `legacy-${chapter.id}-${i}`,
          name: kt.term,
          simpleExplanation: kt.definition,
          detailedExplanation: kt.definition,
          analogy: '',
          realWorldExample: '',
          visualDescription: '',
          icon: 'BookOpen',
          color: 'blue',
          relatedConcepts: [],
          difficulty: 'beginner',
          tags: [],
        })),
        visualAids: [],
        readingTime: Math.ceil(chapter.content.length / 1000),
        difficulty: 'medium',
        topics: [chapter.title],
      });
    }
  }
}
