/**
 * Brain V3 Test Component
 */

import { useState } from 'react';
import { Brain, FileText, Zap, CheckCircle, Clock, Lightbulb, Sparkles, Loader2 } from 'lucide-react';
import { processPDFWithAI } from '../services/ai';
import { db } from '../db';

export default function BrainTest() {
  const [isProcessing, setIsProcessing] = useState(false);
  const [progress, setProgress] = useState('');
  const [results, setResults] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  const runBrainTest = async () => {
    setIsProcessing(true);
    setProgress('Initializing Brain...');
    setError(null);

    try {
      const bookId = `test-book-${Date.now()}`;
      
      console.log('Starting Brain Test...');
      
      const processed = await processPDFWithAI(SAMPLE_TEXT, {
        useELI5: true,
        generateVisuals: true
      });

      console.log('Brain Processing Complete:', processed);
      
      // Save to database
      await db.books.add({
        id: bookId,
        title: 'Brain Test - Real Estate Fundamentals',
        uploadedAt: new Date(),
        totalChapters: processed.chapters.length,
        totalConcepts: processed.concepts.length,
      } as any);

      for (const chapter of processed.chapters) {
        await db.chapters.add(chapter);
      }

      for (const concept of processed.concepts) {
        await db.concepts.add(concept);
      }

      for (const flashcard of processed.flashcards) {
        await db.flashcards.add(flashcard);
      }

      setResults(processed);
      setProgress('Complete!');
      
    } catch (err) {
      console.error('Brain Test Error:', err);
      setError(err instanceof Error ? err.message : 'Unknown error');
    } finally {
      setIsProcessing(false);
    }
  };

  return (
    <div className="space-y-6">
      <div className="text-center space-y-4">
        <div className="inline-flex items-center gap-3 px-6 py-3 glass rounded-2xl border border-violet-500/30">
          <Brain className="w-8 h-8 text-violet-400" />
          <h1 className="text-3xl font-bold bg-gradient-to-r from-violet-400 to-fuchsia-400 bg-clip-text text-transparent">
            Brain V3 Test Lab
          </h1>
        </div>
      </div>

      <div className="glass rounded-2xl p-6">
        <div className="flex items-center gap-3 mb-4">
          <FileText className="w-5 h-5 text-emerald-400" />
          <h2 className="text-xl font-semibold">Test Input</h2>
        </div>
        
        <div className="p-4 bg-slate-800/50 rounded-xl font-mono text-sm text-slate-400 max-h-64 overflow-auto">
          {SAMPLE_TEXT.substring(0, 500)}...
        </div>
        
        <div className="mt-4 flex items-center justify-between">
          <span className="text-sm text-slate-500">{SAMPLE_TEXT.length.toLocaleString()} characters</span>
          
          
          <button
            onClick={runBrainTest}
            disabled={isProcessing}
            className="px-6 py-3 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 disabled:opacity-50 text-white font-medium rounded-xl transition-all flex items-center gap-2"
          >
            {isProcessing ? (
              <><Loader2 className="w-5 h-5 animate-spin" /> Processing...</>
            ) : (
              <><Zap className="w-5 h-5" /> Run Brain Test</>
            )}
          </button>
        </div>
      </div>

      {isProcessing && (
        <div className="glass rounded-2xl p-6 text-center">
          <div className="inline-flex items-center gap-3 text-violet-400">
            <Loader2 className="w-6 h-6 animate-spin" />
            <span>{progress}</span>
          </div>
        </div>
      )}

      {error && (
        <div className="glass rounded-2xl p-6 bg-red-500/10 border border-red-500/30 text-red-400">
          {error}
        </div>
      )}

      {results && (
        <div className="space-y-6">
          <div className="glass rounded-2xl p-6">
            <h2 className="text-xl font-semibold mb-4 flex items-center gap-2">
              <CheckCircle className="w-5 h-5 text-emerald-400" />
              Processing Complete!
            </h2>
            
            <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
              <StatCard icon={FileText} label="Chapters" value={results.chapters.length} color="blue" />
              <StatCard icon={Lightbulb} label="Concepts" value={results.concepts.length} color="yellow" />
              <StatCard icon={Sparkles} label="Flashcards" value={results.flashcards.length} color="purple" />
              <StatCard icon={Clock} label="Visuals" value={results.visualAids.length} color="emerald" />
            </div>
          </div>

          {results.chapters.map((chapter: any) => (
            <div key={chapter.id} className="glass rounded-2xl p-6">
              <h3 className="font-semibold text-lg mb-2">{chapter.title}</h3>
              {chapter.eli5Summary && (
                <div className="p-4 bg-gradient-to-r from-violet-500/10 to-fuchsia-500/10 rounded-lg border border-violet-500/20">
                  <p className="text-sm font-medium text-violet-400 mb-2">ELI5 Summary</p>
                  <p className="text-slate-200">{chapter.eli5Summary.oneSentence}</p>
                  <p className="text-slate-400 text-sm mt-2">{chapter.eli5Summary.shortSummary}</p>
                </div>
              )}
            </div>
          ))}
        </div>
      )}
    </div>
  );
}

function StatCard({ icon: Icon, label, value, color }: any) {
  const colors: Record<string, string> = {
    blue: 'from-blue-500/20 to-cyan-500/20 text-blue-400',
    yellow: 'from-yellow-500/20 to-orange-500/20 text-yellow-400',
    purple: 'from-violet-500/20 to-fuchsia-500/20 text-violet-400',
    emerald: 'from-emerald-500/20 to-teal-500/20 text-emerald-400',
  };

  return (
    <div className={`glass rounded-xl p-4 bg-gradient-to-br ${colors[color]} border border-white/5`}>
      <Icon className="w-6 h-6 mb-2" />
      <p className="text-3xl font-bold">{value}</p>
      <p className="text-sm opacity-80">{label}</p>
    </div>
  );
}

const SAMPLE_TEXT = `Chapter 1: Understanding Real Property

Real property is a fundamental concept in real estate. It includes land and everything permanently attached to it. When you buy real property, you are not just buying dirt - you are buying the land PLUS everything that is attached to it.

Real property includes the land itself, any buildings or structures on the land, and even natural things that are attached like trees and minerals underground.

Chapter 2: Personal Property vs Real Property

Personal property is different from real property. Personal property includes anything you can move around. Your furniture, your car, your clothes, and your television are all personal property.

The key difference is movability. If you can pick it up and take it with you, it is personal property. If it is stuck to the land, it is real property.

Chapter 3: What is a Fixture?

A fixture is something that started as personal property but became so permanently attached to real property that it is now considered part of the real estate.

Think about a chandelier. You can buy a chandelier at a store - at that point, it is personal property. But once you install it and attach it to the ceiling of your house, it becomes a fixture.

Chapter 4: Joint Tenancy

Joint tenancy is when two or more people own property together with equal shares, and if one person dies, their share automatically goes to the surviving owners.

Tenancy in common is different - people can own different percentages, and when one person dies, their share goes to their heirs.`;
