import type { 
  Chapter, Concept, Flashcard, Question, VisualAid,
  ProcessedContent, ConceptMap
} from '../types';

// API Key Management
const API_KEY_STORAGE_KEY = 'tavily_api_key';

export function getApiKey(): string | null {
  return localStorage.getItem(API_KEY_STORAGE_KEY);
}

export function setApiKey(key: string): void {
  localStorage.setItem(API_KEY_STORAGE_KEY, key);
}

export function hasApiKey(): boolean {
  return !!getApiKey();
}

// Semantic chunking
function createSemanticChunks(text: string, bookId: string): Array<{
  id: string;
  text: string;
  heading: string;
  concepts: string[];
}> {
  const chunks: Array<{id: string; text: string; heading: string; concepts: string[]}> = [];
  
  // Split by chapter patterns
  const chapterPattern = /Chapter\s+\d+[\s:.-]+([^\n]+)/gi;
  const parts = text.split(chapterPattern);
  
  let currentHeading = 'Introduction';
  let chunkIndex = 0;
  
  for (let i = 0; i < parts.length; i++) {
    const part = parts[i].trim();
    if (!part) continue;
    
    // Check if this is a heading (odd indices after split contain the captured group)
    if (i % 2 === 1) {
      currentHeading = part;
      continue;
    }
    
    // This is content
    if (part.length > 50) {
      // Extract concepts
      const concepts = extractConcepts(part);
      
      chunks.push({
        id: `chunk-${bookId}-${chunkIndex++}`,
        text: part.substring(0, 2000),
        heading: currentHeading,
        concepts
      });
    }
  }
  
  // If no chapters found, treat whole text as one chunk
  if (chunks.length === 0 && text.length > 50) {
    chunks.push({
      id: `chunk-${bookId}-0`,
      text: text.substring(0, 3000),
      heading: 'Main Content',
      concepts: extractConcepts(text)
    });
  }
  
  return chunks;
}

// Extract concepts from text
function extractConcepts(text: string): string[] {
  const concepts: string[] = [];
  const lowerText = text.toLowerCase();
  
  const conceptKeywords: Record<string, string> = {
    'real property': 'real-property',
    'personal property': 'personal-property',
    'fixture': 'fixture',
    'joint tenancy': 'joint-tenancy',
    'tenancy in common': 'tenancy-common',
    'bundle of rights': 'bundle-rights',
    'mortgage': 'mortgage',
    'contract': 'contract',
    'agency': 'agency',
    'fiduciary': 'fiduciary',
    'escrow': 'escrow',
    'appraisal': 'appraisal',
    'zoning': 'zoning',
  };
  
  for (const [keyword, conceptId] of Object.entries(conceptKeywords)) {
    if (lowerText.includes(keyword)) {
      concepts.push(conceptId);
    }
  }
  
  return [...new Set(concepts)];
}

