// Get ELI5 explanation
function getELI5(conceptId: string): typeof ELI5_DATABASE['default'] {
  return ELI5_DATABASE[conceptId] || ELI5_DATABASE['default'];
}

// Generate flashcards
function generateFlashcards(chunk: any, chapterId: string): Flashcard[] {
  const flashcards: Flashcard[] = [];
  
  for (const conceptId of chunk.concepts) {
    const eli5 = getELI5(conceptId);
    
    // Concept card
    flashcards.push({
      id: `${chunk.id}-card-${flashcards.length}`,
      chapterId,
      conceptId,
      front: `What is ${conceptId.replace(/-/g, ' ')}?`,
      frontType: 'concept',
      back: eli5.oneSentence,
      backType: 'explanation',
      eli5Explanation: eli5.summary,
      realWorldExample: eli5.realWorldExample,
      difficulty: 'easy',
      interval: 1,
      repetition: 0,
      easinessFactor: 2.5,
      nextReviewDate: new Date(),
      timesReviewed: 0,
      timesCorrect: 0
    });
    
    // Analogy card
    flashcards.push({
      id: `${chunk.id}-card-${flashcards.length}`,
      chapterId,
      conceptId,
      front: 'Explain this like I am 5 years old',
      frontType: 'concept',
      back: eli5.analogy,
      backType: 'analogy',
      eli5Explanation: eli5.analogy,
      realWorldExample: eli5.realWorldExample,
      difficulty: 'easy',
      interval: 1,
      repetition: 0,
      easinessFactor: 2.5,
      nextReviewDate: new Date(),
      timesReviewed: 0,
      timesCorrect: 0
    });
  }
  
  return flashcards;
}

// Generate questions
function generateQuestions(chunk: any, chapterId: string): Question[] {
  const questions: Question[] = [];
  
  for (const conceptId of chunk.concepts) {
    const eli5 = getELI5(conceptId);
    
    questions.push({
      id: `${chunk.id}-q-${questions.length}`,
      chapterId,
      conceptId,
      question: `What is the main idea of ${conceptId.replace(/-/g, ' ')}?`,
      questionType: 'conceptual',
      options: [
        eli5.oneSentence,
        'Something only lawyers need to know',
        'A complicated legal procedure',
        'Not important for real estate'
      ],
      correctAnswer: 0,
      explanation: eli5.summary,
      eli5Explanation: eli5.analogy,
      whyCorrect: eli5.whyItMatters,
      whyOthersWrong: ['Incorrect option', 'Not accurate', 'Not applicable'],
      scenario: undefined,
      difficulty: 'easy',
      topic: conceptId,
      tags: [chapterId]
    });
  }
  
  return questions;
}

// Main processing function
export async function processPDFWithAI(
  pdfText: string,
  options: {
    useELI5?: boolean;
    generateVisuals?: boolean;
    generateConceptMap?: boolean;
    difficulty?: 'beginner' | 'intermediate' | 'advanced';
  } = {}
): Promise<ProcessedContent> {
  const { generateVisuals = true } = options;
  
  console.log(' BRAIN: Processing document...');
  
  const bookId = `book-${Date.now()}`;
  
  // Step 1: Create semantic chunks
  const chunks = createSemanticChunks(pdfText, bookId);
  console.log(`   Created ${chunks.length} semantic chunks`);
  
  // Step 2: Process each chunk into chapters
  const chapters: Chapter[] = [];
  const allConcepts: Concept[] = [];
  const allFlashcards: Flashcard[] = [];
  const allQuestions: Question[] = [];
  const allVisuals: VisualAid[] = [];
  
  for (let i = 0; i < chunks.length; i++) {
    const chunk = chunks[i];
    const chapterId = `chapter-${bookId}-${i}`;
    
    console.log(`   Processing Chapter ${i + 1}: ${chunk.heading}`);
    
    // Create concepts
    const concepts: Concept[] = chunk.concepts.map((conceptId, idx) => {
      const eli5 = getELI5(conceptId);
      return {
        id: `${chapterId}-concept-${idx}`,
        name: conceptId.replace(/-/g, ' ').replace(/\b\w/g, l => l.toUpperCase()),
        simpleExplanation: eli5.oneSentence,
        detailedExplanation: eli5.summary,
        analogy: eli5.analogy,
        realWorldExample: eli5.realWorldExample,
        visualDescription: `Illustration showing ${conceptId.replace(/-/g, ' ')} concept`,
        icon: 'Book',
        color: 'blue',
        relatedConcepts: chunk.concepts.filter((c: string) => c !== conceptId),
        difficulty: 'beginner',
        tags: [chunk.heading]
      };
    });
    
    allConcepts.push(...concepts);
    
    // Get primary ELI5
    const primaryELI5 = chunk.concepts.length > 0 
      ? getELI5(chunk.concepts[0]) 
      : getELI5('default');
    
    // Create chapter
    const chapter: Chapter = {
      id: chapterId,
      bookId,
      number: i + 1,
      title: chunk.heading,
      content: chunk.text,
      eli5Summary: {
        oneSentence: primaryELI5.oneSentence,
        shortSummary: primaryELI5.summary,
        whyItMatters: primaryELI5.whyItMatters,
        realWorldConnection: primaryELI5.realWorldExample
      },
      concepts,
      visualAids: generateVisuals ? [{ 
        id: `${chapterId}-visual`,
        type: 'diagram' as const,
        title: `${chunk.heading} Concept Map`,
        description: `Visual guide for ${chunk.heading}`,
        data: { concepts: chunk.concepts }
      }] : [],
      readingTime: Math.ceil(chunk.text.length / 1000),
      difficulty: 'easy',
      topics: [chunk.heading],
      createdAt: new Date(),
      updatedAt: new Date()
    };
    
    chapters.push(chapter);
    
    // Generate flashcards and questions
    const flashcards = generateFlashcards(chunk, chapterId);
    const questions = generateQuestions(chunk, chapterId);
    
    allFlashcards.push(...flashcards);
    allQuestions.push(...questions);
    
    if (generateVisuals) {
      allVisuals.push(...chapter.visualAids);
    }
  }
  
  // Create concept map
  const conceptMap: ConceptMap = {
    id: `map-${bookId}`,
    chapterId: chapters[0]?.id || 'default',
    concepts: allConcepts,
    connections: allConcepts.flatMap(c => 
      c.relatedConcepts.map(rc => ({
        from: c.id,
        to: allConcepts.find(ac => ac.name.toLowerCase().replace(/\s/g, '-') === rc)?.id || rc,
        relationship: 'relates to'
      }))
    )
  };
  
  console.log(' BRAIN: Processing complete!');
  console.log(`   Chapters: ${chapters.length}`);
  console.log(`   Concepts: ${allConcepts.length}`);
  console.log(`   Flashcards: ${allFlashcards.length}`);
  console.log(`   Questions: ${allQuestions.length}`);
  
  return {
    chapters,
    concepts: allConcepts,
    conceptMap,
    flashcards: allFlashcards,
    questions: allQuestions,
    visualAids: allVisuals
  };
}

