import { useState, useEffect, useCallback } from 'react';
import { 
  Brain, RotateCcw, Sparkles,
  CheckCircle, XCircle, Zap, Lightbulb
} from 'lucide-react';
import { db, getDueFlashcards, updateFlashcardAfterReview } from '../db';
import type { Flashcard, Concept } from '../types';

interface FlashcardViewProps {
  initialConcept?: Concept | null;
}

export default function FlashcardView({ initialConcept }: FlashcardViewProps) {
  const [flashcards, setFlashcards] = useState<Flashcard[]>([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [isFlipped, setIsFlipped] = useState(false);
  const [eli5Mode, setEli5Mode] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  const [studyComplete, setStudyComplete] = useState(false);
  const [sessionStats, setSessionStats] = useState({ correct: 0, incorrect: 0 });

  const loadFlashcards = useCallback(async () => {
    setIsLoading(true);
    
    try {
      let cards: Flashcard[];
      
      if (initialConcept) {
        // Load flashcards for specific concept
        cards = await db.flashcards.where('conceptId').equals(initialConcept.id).toArray();
      } else {
        // Load due flashcards
        cards = await getDueFlashcards();
      }
      
      // If no due cards, get some random ones for practice
      if (cards.length === 0) {
        cards = await db.flashcards.limit(20).toArray();
      }
      
      // Shuffle cards
      cards = cards.sort(() => Math.random() - 0.5);
      
      setFlashcards(cards);
      setCurrentIndex(0);
      setIsFlipped(false);
      setStudyComplete(cards.length === 0);
    } catch (error) {
      console.error('Error loading flashcards:', error);
    } finally {
      setIsLoading(false);
    }
  }, [initialConcept]);

  useEffect(() => {
    loadFlashcards();
  }, [loadFlashcards]);

  const handleFlip = () => setIsFlipped(!isFlipped);

  const handleRate = async (quality: number) => {
    const currentCard = flashcards[currentIndex];
    
    // Update stats
    if (quality >= 3) {
      setSessionStats(s => ({ ...s, correct: s.correct + 1 }));
    } else {
      setSessionStats(s => ({ ...s, incorrect: s.incorrect + 1 }));
    }
    
    // Update card in database
    await updateFlashcardAfterReview(currentCard.id, quality);
    
    // Move to next card
    if (currentIndex < flashcards.length - 1) {
      setCurrentIndex(prev => prev + 1);
      setIsFlipped(false);
    } else {
      setStudyComplete(true);
    }
  };

  const handleRestart = () => {
    setCurrentIndex(0);
    setIsFlipped(false);
    setStudyComplete(false);
    setSessionStats({ correct: 0, incorrect: 0 });
    loadFlashcards();
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="glass rounded-2xl p-8 text-center">
          <Brain className="w-12 h-12 mx-auto mb-3 text-violet-400 animate-pulse" />
          <p className="text-slate-400">Loading flashcards...</p>
        </div>
      </div>
    );
  }

  if (studyComplete) {
    return (
      <StudyComplete 
        stats={sessionStats} 
        total={flashcards.length}
        onRestart={handleRestart}
      />
    );
  }

  if (flashcards.length === 0) {
    return (
      <div className="glass rounded-2xl p-8 text-center">
        <Brain className="w-16 h-16 mx-auto mb-4 text-slate-500" />
        <h3 className="text-xl font-semibold text-white mb-2">No Flashcards Yet</h3>
        <p className="text-slate-400 mb-4">Upload a PDF to generate flashcards from your study material.</p>
      </div>
    );
  }

  const currentCard = flashcards[currentIndex];
  const progress = ((currentIndex + 1) / flashcards.length) * 100;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-xl font-bold text-white">
            {initialConcept ? `Studying: ${initialConcept.name}` : 'Flashcard Review'}
          </h2>
          <p className="text-sm text-slate-400">Card {currentIndex + 1} of {flashcards.length}</p>
        </div>
        
        <button
          onClick={() => setEli5Mode(!eli5Mode)}
          className={`flex items-center gap-2 px-4 py-2 rounded-xl transition-all ${
            eli5Mode 
              ? 'bg-emerald-500/20 text-emerald-400 border border-emerald-500/30' 
              : 'glass text-slate-400'
          }`}
        >
          <Sparkles className="w-4 h-4" />
          {eli5Mode ? 'ELI5 Mode' : 'Standard'}
        </button>
      </div>

      {/* Progress Bar */}
      <div className="glass rounded-full h-2 overflow-hidden">
        <div 
          className="h-full bg-gradient-to-r from-violet-500 to-fuchsia-500 transition-all duration-300"
          style={{ width: `${progress}%` }}
        />
      </div>

      {/* Flashcard */}
      <div className="relative h-80 sm:h-96">
        <div 
          onClick={handleFlip}
          className={`absolute inset-0 glass rounded-2xl p-8 cursor-pointer transition-all duration-500 transform-gpu preserve-3d ${
            isFlipped ? 'rotate-y-180' : ''
          }`}
          style={{ transformStyle: 'preserve-3d' }}
        >
          {/* Front */}
          <div 
            className="absolute inset-0 backface-hidden flex flex-col items-center justify-center p-8"
            style={{ backfaceVisibility: 'hidden' }}
          >
            <div className="w-12 h-12 rounded-xl bg-violet-500/20 flex items-center justify-center mb-4">
              <Brain className="w-6 h-6 text-violet-400" />
            </div>
            <p className="text-2xl sm:text-3xl font-semibold text-white text-center">
              {currentCard.front}
            </p>
            
            <p className="text-slate-500 text-sm mt-6">Click to reveal answer</p>
          </div>

          {/* Back */}
          <div 
            className="absolute inset-0 backface-hidden flex flex-col items-center justify-center p-8 rotate-y-180"
            style={{ backfaceVisibility: 'hidden', transform: 'rotateY(180deg)' }}
          >
            {eli5Mode && currentCard.eli5Explanation ? (
              <div className="text-center w-full max-h-full overflow-y-auto">
                <div className="w-12 h-12 rounded-xl bg-emerald-500/20 flex items-center justify-center mx-auto mb-4">
                  <Sparkles className="w-6 h-6 text-emerald-400" />
                </div>
                <p className="text-xl text-emerald-100 mb-4">{currentCard.eli5Explanation}</p>
                
                {currentCard.realWorldExample && (
                  <div className="glass rounded-xl p-4 mt-4">
                    <p className="text-sm text-amber-400 font-medium mb-1"> Real World Example</p>
                    <p className="text-slate-300 text-sm">{currentCard.realWorldExample}</p>
                  </div>
                )}
              </div>
            ) : (
              <>
                <div className="w-12 h-12 rounded-xl bg-fuchsia-500/20 flex items-center justify-center mb-4">
                  <Lightbulb className="w-6 h-6 text-fuchsia-400" />
                </div>
                <p className="text-xl sm:text-2xl text-white text-center">{currentCard.back}</p>
              </>
            )}
          </div>
        </div>
      </div>

      {/* Rating Buttons */}
      {isFlipped && (
        <div className="grid grid-cols-4 gap-3">
          {[
            { quality: 1, label: 'Again', color: 'red', icon: XCircle },
            { quality: 2, label: 'Hard', color: 'orange', icon: Zap },
            { quality: 3, label: 'Good', color: 'blue', icon: CheckCircle },
            { quality: 4, label: 'Easy', color: 'emerald', icon: CheckCircle },
          ].map(({ quality, label, color, icon: Icon }) => (
            <button
              key={quality}
              onClick={() => handleRate(quality)}
              className={`py-3 rounded-xl font-medium transition-all flex flex-col items-center gap-1 ${
                color === 'red' ? 'bg-red-500/20 text-red-400 hover:bg-red-500/30 border border-red-500/30' :
                color === 'orange' ? 'bg-orange-500/20 text-orange-400 hover:bg-orange-500/30 border border-orange-500/30' :
                color === 'blue' ? 'bg-blue-500/20 text-blue-400 hover:bg-blue-500/30 border border-blue-500/30' :
                'bg-emerald-500/20 text-emerald-400 hover:bg-emerald-500/30 border border-emerald-500/30'
              }`}
            >
              <Icon className="w-5 h-5" />
              {label}
            </button>
          ))}
        </div>
      )}

      {!isFlipped && (
        <button
          onClick={handleFlip}
          className="w-full py-4 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-semibold rounded-xl transition-all flex items-center justify-center gap-2"
        >
          <RotateCcw className="w-5 h-5" />
          Show Answer
        </button>
      )}

      {/* Stats */}
      <div className="glass rounded-xl p-4 flex items-center justify-center gap-8">
        <div className="text-center">
          <p className="text-2xl font-bold text-emerald-400">{sessionStats.correct}</p>
          <p className="text-xs text-slate-500">Correct</p>
        </div>
        <div className="w-px h-10 bg-white/10" />
        <div className="text-center">
          <p className="text-2xl font-bold text-red-400">{sessionStats.incorrect}</p>
          <p className="text-xs text-slate-500">To Review</p>
        </div>
        <div className="w-px h-10 bg-white/10" />
        <div className="text-center">
          <p className="text-2xl font-bold text-violet-400">{Math.round((sessionStats.correct / (sessionStats.correct + sessionStats.incorrect || 1)) * 100)}%</p>
          <p className="text-xs text-slate-500">Accuracy</p>
        </div>
      </div>
    </div>
  );
}

function StudyComplete({ stats, total, onRestart }: { 
  stats: { correct: number; incorrect: number }; 
  total: number;
  onRestart: () => void;
}) {
  const accuracy = Math.round((stats.correct / (stats.correct + stats.incorrect || 1)) * 100);
  
  return (
    <div className="glass rounded-2xl p-8 text-center">
      <div className="w-20 h-20 mx-auto mb-6 rounded-2xl bg-gradient-to-br from-emerald-500 to-teal-500 flex items-center justify-center shadow-lg shadow-emerald-500/25">
        <CheckCircle className="w-10 h-10 text-white" />
      </div>
      
      <h3 className="text-2xl font-bold text-white mb-2">Session Complete! </h3>
      <p className="text-slate-400 mb-6">You reviewed {total} flashcards</p>
      
      <div className="grid grid-cols-3 gap-4 mb-8">
        <div className="glass rounded-xl p-4">
          <p className="text-3xl font-bold text-emerald-400">{stats.correct}</p>
          <p className="text-sm text-slate-500">Correct</p>
        </div>
        <div className="glass rounded-xl p-4">
          <p className="text-3xl font-bold text-red-400">{stats.incorrect}</p>
          <p className="text-sm text-slate-500">To Review</p>
        </div>
        <div className="glass rounded-xl p-4">
          <p className="text-3xl font-bold text-violet-400">{accuracy}%</p>
          <p className="text-sm text-slate-500">Accuracy</p>
        </div>
      </div>
      
      <button
        onClick={onRestart}
        className="px-8 py-4 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-semibold rounded-xl transition-all flex items-center justify-center gap-2 mx-auto"
      >
        <RotateCcw className="w-5 h-5" />
        Study More Cards
      </button>
    </div>
  );
}
