import { useState } from 'react';
import { Key, Shield, Sparkles, ExternalLink, CheckCircle, AlertTriangle } from 'lucide-react';
import { setApiKey } from '../services/ai';

interface APIKeySetupProps {
  onComplete: () => void;
  onSkip?: () => void;
}

export default function APIKeySetup({ onComplete, onSkip }: APIKeySetupProps) {
  const [apiKey, setApiKeyInput] = useState('');
  const [isSaving, setIsSaving] = useState(false);
  const [showKey, setShowKey] = useState(false);
  const [error, setError] = useState<string | null>(null);

  const handleSave = async () => {
    if (!apiKey.trim()) {
      setError('Please enter an API key');
      return;
    }

    setIsSaving(true);
    setError(null);

    try {
      // Test the key with a simple request
      const response = await fetch('https://api.tavily.com/search', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          api_key: apiKey.trim(),
          query: 'test',
          max_results: 1,
        }),
      });

      if (response.status === 401) {
        setError('Invalid API key. Please check and try again.');
        setIsSaving(false);
        return;
      }

      // Save the key
      setApiKey(apiKey.trim());
      onComplete();
    } catch (err) {
      // Network error - still save the key, it might work later
      setApiKey(apiKey.trim());
      onComplete();
    } finally {
      setIsSaving(false);
    }
  };

  const handleSkip = () => {
    if (onSkip) onSkip();
  };

  return (
    <div className="min-h-screen flex items-center justify-center p-4">
      <div className="w-full max-w-md">
        <div className="glass rounded-2xl p-8">
          {/* Header */}
          <div className="text-center mb-8">
            <div className="w-16 h-16 mx-auto mb-4 rounded-2xl bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center shadow-lg shadow-violet-500/25">
              <Key className="w-8 h-8 text-white" />
            </div>
            
            <h1 className="text-2xl font-bold text-white mb-2">API Key Setup</h1>
            <p className="text-slate-400">Connect to Tavily AI for enhanced study features</p>
          </div>

          {/* Benefits */}
          <div className="space-y-3 mb-6">
            {[
              'Deep AI content analysis with ELI5 explanations',
              'Smart flashcard generation with analogies',
              'Market analysis and comps reports',
              'Scenario-based practice questions',
            ].map((benefit, i) => (
              <div key={i} className="flex items-center gap-3 text-slate-300">
                <CheckCircle className="w-5 h-5 text-emerald-400 flex-shrink-0" />
                <span className="text-sm">{benefit}</span>
              </div>
            ))}
          </div>

          {/* Input */}
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium text-slate-300 mb-2">Tavily API Key</label>
              <div className="relative">
                <input
                  type={showKey ? 'text' : 'password'}
                  value={apiKey}
                  onChange={(e) => setApiKeyInput(e.target.value)}
                  placeholder="tvly-..."
                  className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white placeholder-slate-500 focus:ring-2 focus:ring-violet-500/50 pr-24"
                />
                <button
                  type="button"
                  onClick={() => setShowKey(!showKey)}
                  className="absolute right-3 top-1/2 -translate-y-1/2 text-slate-400 hover:text-white text-sm"
                >
                  {showKey ? 'Hide' : 'Show'}
                </button>
              </div>
              
              <p className="text-xs text-slate-500 mt-2">
                Your key is stored only in your browser's localStorage.
              </p>
            </div>

            {error && (
              <div className="p-3 bg-red-500/10 border border-red-500/30 rounded-xl flex items-center gap-2 text-red-300">
                <AlertTriangle className="w-4 h-4" />
                <span className="text-sm">{error}</span>
              </div>
            )}

            <button
              onClick={handleSave}
              disabled={isSaving || !apiKey.trim()}
              className="w-full py-3 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 disabled:opacity-50 text-white font-medium rounded-xl transition-all flex items-center justify-center gap-2"
            >
              {isSaving ? (
                <>Verifying...</>
              ) : (
                <><Sparkles className="w-4 h-4" /> Connect</>
              )}
            </button>

            {onSkip && (
              <button
                onClick={handleSkip}
                className="w-full py-3 glass hover:bg-white/5 text-slate-400 font-medium rounded-xl transition-all"
              >
                Skip for now
              </button>
            )}
          </div>

          {/* Get Key Link */}
          <div className="mt-6 pt-6 border-t border-white/10 text-center">
            <p className="text-sm text-slate-400 mb-2">Don't have an API key?</p>
            <a
              href="https://tavily.com"
              target="_blank"
              rel="noopener noreferrer"
              className="inline-flex items-center gap-1 text-violet-400 hover:text-violet-300 transition-colors"
            >
              Get one free at Tavily
              <ExternalLink className="w-4 h-4" />
            </a>
          </div>
        </div>

        {/* Security Note */}
        <div className="mt-4 flex items-start gap-2 text-slate-500 text-xs">
          <Shield className="w-4 h-4 flex-shrink-0 mt-0.5" />
          <p>Your API key never leaves your browser. It's stored securely in localStorage and used only for direct API calls to Tavily.</p>
        </div>
      </div>
    </div>
  );
}
