import { useState, useEffect } from 'react';
import { Calendar, CheckCircle, AlertCircle, Sparkles } from 'lucide-react';
import { db } from '../db';
import type { StudySchedule, DailyGoal } from '../types';

export default function StudySchedule() {
  const [schedule, setSchedule] = useState<StudySchedule | null>(null);
  const [examDate, setExamDate] = useState('');
  const [isCreating, setIsCreating] = useState(false);

  useEffect(() => {
    loadSchedule();
  }, []);

  const loadSchedule = async () => {
    const existing = await db.studySchedules.toArray();
    if (existing.length > 0) {
      setSchedule(existing[0]);
    }
  };

  const createSchedule = async () => {
    if (!examDate) return;
    
    setIsCreating(true);
    
    const targetDate = new Date(examDate);
    const today = new Date();
    const daysUntilExam = Math.ceil((targetDate.getTime() - today.getTime()) / (1000 * 60 * 60 * 24));
    
    // Generate daily goals
    const dailyGoals: DailyGoal[] = [];
    const chapters = await db.chapters.toArray();
    const concepts = await db.concepts.toArray();
    
    const chaptersPerDay = Math.ceil(chapters.length / Math.max(daysUntilExam, 1));
    
    for (let i = 0; i < daysUntilExam; i++) {
      const date = new Date(today);
      date.setDate(date.getDate() + i);
      
      const startIdx = i * chaptersPerDay;
      const dayChapters = chapters.slice(startIdx, startIdx + chaptersPerDay);
      const dayConcepts = concepts.filter(c => dayChapters.some(ch => ch.concepts?.some(con => con.id === c.id)));
      
      dailyGoals.push({
        date,
        chapters: dayChapters.map(c => c.id),
        concepts: dayConcepts.map(c => c.id),
        flashcards: 10,
        quizQuestions: 5,
        completed: false,
        eli5Mode: true,
      });
    }
    
    const newSchedule: StudySchedule = {
      id: crypto.randomUUID(),
      examDate: targetDate,
      dailyGoals,
      createdAt: new Date(),
      focusAreas: [],
      weakConcepts: [],
    };
    
    await db.studySchedules.add(newSchedule);
    setSchedule(newSchedule);
    setIsCreating(false);
  };

  const toggleGoalComplete = async (index: number) => {
    if (!schedule) return;
    
    const updatedGoals = [...schedule.dailyGoals];
    updatedGoals[index].completed = !updatedGoals[index].completed;
    
    await db.studySchedules.update(schedule.id, { dailyGoals: updatedGoals });
    setSchedule({ ...schedule, dailyGoals: updatedGoals });
  };

  if (!schedule) {
    return (
      <div className="space-y-6">
        <div className="glass rounded-2xl p-8 text-center">
          <div className="w-16 h-16 mx-auto mb-4 rounded-2xl bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center">
            <Calendar className="w-8 h-8 text-white" />
          </div>
          
          <h2 className="text-2xl font-bold text-white mb-2">Create Study Plan</h2>
          <p className="text-slate-400 mb-6">Set your exam date and we'll create a personalized study schedule</p>
          
          <div className="max-w-sm mx-auto">
            <label className="block text-sm font-medium text-slate-300 mb-2 text-left">Exam Date</label>
            <input
              type="date"
              value={examDate}
              onChange={(e) => setExamDate(e.target.value)}
              className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white mb-4"
            />
            
            <button
              onClick={createSchedule}
              disabled={!examDate || isCreating}
              className="w-full py-3 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 disabled:opacity-50 text-white font-medium rounded-xl transition-all flex items-center justify-center gap-2"
            >
              {isCreating ? (
                <>Creating...</>
              ) : (
                <><Sparkles className="w-4 h-4" /> Generate Schedule</>
              )}
            </button>
          </div>
        </div>
      </div>
    );
  }

  const today = new Date();
  const daysUntilExam = Math.ceil((new Date(schedule.examDate).getTime() - today.getTime()) / (1000 * 60 * 60 * 24));
  const completedGoals = schedule.dailyGoals.filter(g => g.completed).length;
  const progress = (completedGoals / schedule.dailyGoals.length) * 100;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="glass rounded-2xl p-6">
        <div className="flex items-center justify-between flex-wrap gap-4">
          <div>
            <h2 className="text-2xl font-bold text-white">Study Schedule</h2>
            <p className="text-slate-400">Exam in {daysUntilExam} days  {completedGoals}/{schedule.dailyGoals.length} days completed</p>
          </div>
          
          <div className="glass rounded-xl px-4 py-2">
            <span className="text-2xl font-bold text-violet-400">{Math.round(progress)}%</span>
            <span className="text-slate-500 text-sm ml-2">complete</span>
          </div>
        </div>
        
        <div className="mt-4 glass rounded-full h-2 overflow-hidden">
          <div 
            className="h-full bg-gradient-to-r from-violet-500 to-fuchsia-500 transition-all"
            style={{ width: `${progress}%` }}
          />
        </div>
      </div>

      {/* Daily Goals */}
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {schedule.dailyGoals.slice(0, 14).map((goal, index) => {
          const isToday = new Date(goal.date).toDateString() === today.toDateString();
          const isPast = new Date(goal.date) < today;
          
          return (
            <div 
              key={index}
              className={`glass rounded-xl p-4 transition-all ${
                goal.completed ? 'opacity-50' : ''
              } ${isToday ? 'ring-2 ring-violet-500/50' : ''}`}
            >
              <div className="flex items-start justify-between mb-3">
                <div>
                  <p className={`font-semibold ${isToday ? 'text-violet-300' : 'text-white'}`}>
                    {isToday ? 'Today' : new Date(goal.date).toLocaleDateString('en-US', { weekday: 'short', month: 'short', day: 'numeric' })}
                  </p>
                  {isPast && !goal.completed && (
                    <span className="text-xs text-red-400 flex items-center gap-1">
                      <AlertCircle className="w-3 h-3" /> Missed
                    </span>
                  )}
                </div>
                
                <button
                  onClick={() => toggleGoalComplete(index)}
                  className={`w-6 h-6 rounded-full border-2 flex items-center justify-center transition-all ${
                    goal.completed 
                      ? 'bg-emerald-500 border-emerald-500' 
                      : 'border-slate-500 hover:border-violet-500'
                  }`}
                >
                  {goal.completed && <CheckCircle className="w-4 h-4 text-white" />}
                </button>
              </div>
              
              <div className="space-y-1 text-sm text-slate-400">
                <p>{goal.chapters.length} chapters</p>
                <p>{goal.flashcards} flashcards</p>
                <p>{goal.quizQuestions} quiz questions</p>
              </div>
            </div>
          );
        })}
      </div>
    </div>
  );
}
