import { useState } from 'react';
import { 
  Upload, FileText, BookOpen, CheckCircle, AlertCircle, 
  Sparkles, Zap 
} from 'lucide-react';
import { processPDFWithAI } from '../services/ai';
import { db } from '../db';
import * as pdfUtils from '../utils/pdf';

interface PDFUploadProps {
  onUploadComplete: () => void;
}

export default function PDFUpload({ onUploadComplete }: PDFUploadProps) {
  const [isDragging, setIsDragging] = useState(false);
  const [isProcessing, setIsProcessing] = useState(false);
  const [progress, setProgress] = useState(0);
  const [status, setStatus] = useState<string>('');
  const [error, setError] = useState<string | null>(null);

  const handleDragOver = (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(true);
  };

  const handleDragLeave = () => {
    setIsDragging(false);
  };

  const handleDrop = async (e: React.DragEvent) => {
    e.preventDefault();
    setIsDragging(false);
    
    const files = Array.from(e.dataTransfer.files).filter(f => f.type === 'application/pdf');
    if (files.length > 0) {
      await processFile(files[0]);
    }
  };

  const handleFileSelect = async (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0];
    if (file) {
      await processFile(file);
    }
  };

  const processFile = async (file: File) => {
    setIsProcessing(true);
    setError(null);
    setProgress(0);
    
    try {
      // Step 1: Extract text
      setStatus('Extracting text from PDF...');
      setProgress(10);
      
      const pdfText = await pdfUtils.extractTextFromPDF(file);
      
      if (!pdfText || pdfText.length < 100) {
        throw new Error('Could not extract sufficient text from PDF. The file may be scanned images or encrypted.');
      }

      // Step 2: Create book record
      setStatus('Creating study material...');
      setProgress(30);
      
      const bookId = crypto.randomUUID();
      const book = {
        id: bookId,
        title: file.name.replace('.pdf', ''),
        totalPages: pdfText.length / 3000, // Estimate
        uploadedAt: new Date(),
        chapters: [],
        totalConcepts: 0,
      };

      await db.books.add(book);

      // Step 3: AI Processing with deep comprehension
      setStatus(' AI is deeply analyzing content...');
      setProgress(50);
      
      const processedContent = await processPDFWithAI(pdfText, {
        useELI5: true,
        generateVisuals: true,
        generateConceptMap: true,
        difficulty: 'beginner',
      });

      // Step 4: Save chapters
      setStatus('Saving chapters...');
      setProgress(70);
      
      const chapterIds: string[] = [];
      
      for (const chapter of processedContent.chapters) {
        chapter.bookId = bookId;
        await db.chapters.add(chapter);
        chapterIds.push(chapter.id);
      }

      // Step 5: Save concepts
      setStatus('Building concept database...');
      setProgress(80);
      
      for (const concept of processedContent.concepts) {
        await db.concepts.add(concept);
      }

      // Step 6: Save flashcards and questions
      setStatus('Creating flashcards & quizzes...');
      setProgress(90);
      
      for (const flashcard of processedContent.flashcards) {
        await db.flashcards.add(flashcard);
      }

      for (const question of processedContent.questions) {
        await db.questions.add(question);
      }

      // Step 7: Update book
      setStatus('Finalizing...');
      setProgress(95);
      
      await db.books.update(bookId, {
        chapters: chapterIds,
        totalConcepts: processedContent.concepts.length,
        conceptMap: processedContent.conceptMap,
      });

      // Step 8: Create initial progress record
      await db.progress.add({
        id: crypto.randomUUID(),
        bookId,
        chaptersRead: [],
        conceptsMastered: [],
        flashcardsReviewed: 0,
        quizzesTaken: 0,
        averageScore: 0,
        weakTopics: [],
        strongTopics: [],
        lastStudied: new Date(),
        conceptProgress: processedContent.concepts.map(c => ({
          conceptId: c.id,
          mastery: 0,
          lastReviewed: new Date(),
        })),
      });

      setProgress(100);
      setStatus('Complete!');
      
      setTimeout(() => {
        onUploadComplete();
        setIsProcessing(false);
      }, 500);

    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to process PDF');
      setIsProcessing(false);
    }
  };

  return (
    <div className="space-y-6">
      {/* Welcome Header */}
      <div className="glass rounded-2xl p-8 text-center">
        <div className="w-16 h-16 mx-auto mb-4 rounded-2xl bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center shadow-lg shadow-violet-500/25">
          <Sparkles className="w-8 h-8 text-white" />
        </div>
        <h2 className="text-2xl font-bold text-white mb-2">Welcome to RE Study AI</h2>
        <p className="text-slate-400 max-w-md mx-auto">
          Upload your real estate textbooks and let our AI create personalized study materials 
          with ELI5 explanations, visual aids, and smart flashcards.
        </p>
      </div>

      {/* Upload Zone */}
      <div
        onDragOver={handleDragOver}
        onDragLeave={handleDragLeave}
        onDrop={handleDrop}
        className={`relative glass rounded-2xl p-8 text-center transition-all duration-300 ${
          isDragging ? 'border-2 border-violet-500 bg-violet-500/10' : 'border-2 border-dashed border-white/20'
        } ${isProcessing ? 'pointer-events-none' : ''}`}
      >
        {isProcessing ? (
          <div className="py-8">
            <div className="w-16 h-16 mx-auto mb-4 relative">
              <svg className="w-16 h-16 transform -rotate-90" viewBox="0 0 64 64">
                <circle cx="32" cy="32" r="28" fill="none" stroke="rgba(255,255,255,0.1)" strokeWidth="4" />
                <circle 
                  cx="32" cy="32" r="28" fill="none" 
                  stroke="url(#gradient)" strokeWidth="4"
                  strokeDasharray={`${progress * 1.76} 176`}
                  strokeLinecap="round"
                />
                <defs>
                  <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%">
                    <stop offset="0%" stopColor="#8b5cf6" />
                    <stop offset="100%" stopColor="#d946ef" />
                  </linearGradient>
                </defs>
              </svg>
              <div className="absolute inset-0 flex items-center justify-center">
                <span className="text-lg font-bold text-white">{progress}%</span>
              </div>
            </div>
            <p className="text-violet-300 font-medium animate-pulse">{status}</p>
            <p className="text-slate-500 text-sm mt-2">This may take a minute for large files</p>
          </div>
        ) : (
          <>
            <div className="w-16 h-16 mx-auto mb-4 rounded-2xl glass flex items-center justify-center">
              <Upload className="w-8 h-8 text-violet-400" />
            </div>
            <h3 className="text-xl font-semibold text-white mb-2">Upload Your PDF</h3>
            <p className="text-slate-400 mb-6">Drag and drop your real estate textbook, or click to browse</p>
            <label className="inline-flex items-center gap-2 px-6 py-3 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-medium rounded-xl cursor-pointer transition-all shadow-lg shadow-violet-500/25">
              <FileText className="w-5 h-5" />
              Select PDF File
              <input type="file" accept=".pdf" onChange={handleFileSelect} className="hidden" />
            </label>
            <p className="text-slate-500 text-sm mt-4">Supports PDF files up to 50MB</p>
          </>
        )}
      </div>

      {error && (
        <div className="glass rounded-xl p-4 bg-red-500/10 border border-red-500/30">
          <div className="flex items-start gap-3">
            <AlertCircle className="w-5 h-5 text-red-400 mt-0.5" />
            <div>
              <p className="text-red-300 font-medium">Processing Error</p>
              <p className="text-red-200/70 text-sm">{error}</p>
            </div>
          </div>
        </div>
      )}

      {/* Features Grid */}
      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {[
          { icon: Zap, title: 'Deep AI Analysis', desc: 'Not just text extraction - true understanding with ELI5 explanations' },
          { icon: BookOpen, title: 'Smart Flashcards', desc: 'Auto-generated with analogies, examples, and spaced repetition' },
          { icon: CheckCircle, title: 'Practice Quizzes', desc: 'Scenario-based questions that teach while testing' },
        ].map((feature, idx) => (
          <div key={idx} className="glass rounded-xl p-4">
            <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-violet-500/20 to-fuchsia-500/20 flex items-center justify-center mb-3">
              <feature.icon className="w-5 h-5 text-violet-400" />
            </div>
            <h4 className="font-semibold text-white mb-1">{feature.title}</h4>
            <p className="text-slate-400 text-sm">{feature.desc}</p>
          </div>
        ))}
      </div>

      {/* How It Works */}
      <div className="glass rounded-2xl p-6">
        <h3 className="text-lg font-semibold text-white mb-4">How It Works</h3>
        <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
          {[
            { step: 1, title: 'Upload PDF', desc: 'Drop your textbook or course material' },
            { step: 2, title: 'AI Reads', desc: 'Our AI deeply analyzes every concept' },
            { step: 3, title: 'ELI5 Mode', desc: 'Get simple explanations with analogies' },
            { step: 4, title: 'Study Smart', desc: 'Flashcards, quizzes & progress tracking' },
          ].map((item) => (
            <div key={item.step} className="flex items-start gap-3">
              <div className="w-8 h-8 rounded-full bg-violet-500/20 flex items-center justify-center flex-shrink-0">
                <span className="text-sm font-bold text-violet-400">{item.step}</span>
              </div>
              <div>
                <h4 className="font-medium text-white text-sm">{item.title}</h4>
                <p className="text-slate-500 text-xs">{item.desc}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
