import { useState } from 'react';
import { Book, ChevronRight, Clock, Lightbulb, BarChart3, Sparkles } from 'lucide-react';
import type { Book as BookType, Chapter } from '../types';

interface ChapterListProps {
  book: BookType;
  chapters: Chapter[];
  onChapterSelect: (chapter: Chapter) => void;
  onChaptersUpdated: (bookId: string) => void;
}

export default function ChapterList({ book, chapters, onChapterSelect }: ChapterListProps) {
  const [expandedChapter, setExpandedChapter] = useState<string | null>(null);

  const totalConcepts = chapters.reduce((acc, ch) => acc + (ch.concepts?.length || 0), 0);
  const totalReadingTime = chapters.reduce((acc, ch) => acc + (ch.readingTime || 0), 0);

  return (
    <div className="space-y-6">
      {/* Book Header */}
      <div className="glass rounded-2xl p-6">
        <div className="flex items-start gap-4">
          <div className="w-14 h-14 rounded-2xl bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center shadow-lg shadow-violet-500/25 flex-shrink-0">
            <Book className="w-7 h-7 text-white" />
          </div>
          <div className="flex-1">
            <h2 className="text-xl font-bold text-white mb-1">{book.title}</h2>
            <div className="flex flex-wrap gap-3 text-sm text-slate-400">
              <span className="flex items-center gap-1">
                <BarChart3 className="w-4 h-4" />
                {chapters.length} chapters
              </span>
              <span className="flex items-center gap-1">
                <Lightbulb className="w-4 h-4" />
                {totalConcepts} concepts
              </span>
              <span className="flex items-center gap-1">
                <Clock className="w-4 h-4" />
                ~{totalReadingTime} min read
              </span>
            </div>
          </div>
        </div>
      </div>

      {/* Chapters Grid */}
      <div className="grid grid-cols-1 gap-4">
        {chapters.length === 0 ? (
          <div className="glass rounded-2xl p-8 text-center">
            <Sparkles className="w-12 h-12 mx-auto mb-3 text-slate-500" />
            <p className="text-slate-400">No chapters found. Try uploading your PDF again.</p>
          </div>
        ) : (
          chapters.map((chapter, index) => (
            <ChapterCard
              key={chapter.id}
              chapter={chapter}
              index={index}
              isExpanded={expandedChapter === chapter.id}
              onToggle={() => setExpandedChapter(expandedChapter === chapter.id ? null : chapter.id)}
              onSelect={() => onChapterSelect(chapter)}
            />
          ))
        )}
      </div>
    </div>
  );
}

interface ChapterCardProps {
  chapter: Chapter;
  index: number;
  isExpanded: boolean;
  onToggle: () => void;
  onSelect: () => void;
}

function ChapterCard({ chapter, index, isExpanded, onToggle, onSelect }: ChapterCardProps) {
  const conceptCount = chapter.concepts?.length || 0;
  
  return (
    <div className={`glass rounded-2xl overflow-hidden transition-all duration-300 ${isExpanded ? 'ring-2 ring-violet-500/50' : ''}`}>
      <div 
        className="p-5 cursor-pointer hover:bg-white/5 transition-colors"
        onClick={onToggle}
      >
        <div className="flex items-start gap-4">
          <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-violet-500/20 to-fuchsia-500/20 flex items-center justify-center flex-shrink-0">
            <span className="text-sm font-bold text-violet-400">{index + 1}</span>
          </div>
          
          <div className="flex-1">
            <h3 className="font-semibold text-white mb-1">{chapter.title}</h3>
            
            {chapter.eli5Summary && (
              <p className="text-slate-400 text-sm line-clamp-2 mb-2">
                {chapter.eli5Summary.oneSentence}
              </p>
            )}
            
            <div className="flex flex-wrap items-center gap-3 text-xs text-slate-500">
              <span className="flex items-center gap-1">
                <Lightbulb className="w-3 h-3" />
                {conceptCount} concepts
              </span>
              {chapter.readingTime && (
                <span className="flex items-center gap-1">
                  <Clock className="w-3 h-3" />
                  {chapter.readingTime} min
                </span>
              )}
              <span className={`px-2 py-0.5 rounded-full ${
                chapter.difficulty === 'easy' ? 'bg-emerald-500/20 text-emerald-400' :
                chapter.difficulty === 'medium' ? 'bg-yellow-500/20 text-yellow-400' :
                'bg-red-500/20 text-red-400'
              }`}>
                {chapter.difficulty}
              </span>
            </div>
          </div>
          
          <ChevronRight className={`w-5 h-5 text-slate-500 transition-transform duration-300 ${isExpanded ? 'rotate-90' : ''}`} />
        </div>
      </div>
      
      {isExpanded && (
        <div className="px-5 pb-5 border-t border-white/10">
          <div className="pt-4 space-y-4">
            {chapter.eli5Summary && (
              <div className="bg-emerald-500/10 border border-emerald-500/30 rounded-xl p-4">
                <p className="text-sm text-emerald-400 font-medium mb-1"> ELI5 Summary</p>
                <p className="text-emerald-100">{chapter.eli5Summary.shortSummary}</p>
              </div>
            )}
            
            <div className="flex flex-wrap gap-2">
              {chapter.topics?.map((topic, i) => (
                <span key={i} className="px-3 py-1 text-xs bg-white/5 rounded-full text-slate-400">
                  {topic}
                </span>
              ))}
            </div>
            
            <button
              onClick={(e) => { e.stopPropagation(); onSelect(); }}
              className="w-full py-3 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-medium rounded-xl transition-all flex items-center justify-center gap-2"
            >
              <Sparkles className="w-4 h-4" />
              Study This Chapter
            </button>
          </div>
        </div>
      )}
    </div>
  );
}
