import { useState, useEffect, useCallback } from 'react';
import { 
  Brain, CheckCircle, XCircle, ArrowRight, RotateCcw,
  Sparkles, Trophy, Lightbulb
} from 'lucide-react';
import { db } from '../db';
import type { Question, Chapter, QuizResult } from '../types';

interface QuizViewProps {
  chapters: Chapter[];
  selectedChapter?: Chapter | null;
}

export default function QuizView({ selectedChapter }: QuizViewProps) {
  const [questions, setQuestions] = useState<Question[]>([]);
  const [currentIndex, setCurrentIndex] = useState(0);
  const [selectedAnswer, setSelectedAnswer] = useState<number | null>(null);
  const [showExplanation, setShowExplanation] = useState(false);
  const [eli5Mode, setEli5Mode] = useState(true);
  const [isLoading, setIsLoading] = useState(true);
  const [quizComplete, setQuizComplete] = useState(false);
  const [answers, setAnswers] = useState<{ questionId: string; correct: boolean; selected: number }[]>([]);
  const [startTime, setStartTime] = useState(Date.now());

  const loadQuestions = useCallback(async () => {
    setIsLoading(true);
    
    try {
      let qs: Question[];
      
      if (selectedChapter) {
        qs = await db.questions.where('chapterId').equals(selectedChapter.id).toArray();
      } else {
        qs = await db.questions.limit(20).toArray();
      }
      
      // Shuffle and take 10
      qs = qs.sort(() => Math.random() - 0.5).slice(0, 10);
      
      setQuestions(qs);
      setCurrentIndex(0);
      setSelectedAnswer(null);
      setShowExplanation(false);
      setQuizComplete(qs.length === 0);
      setAnswers([]);
      setStartTime(Date.now());
    } catch (error) {
      console.error('Error loading questions:', error);
    } finally {
      setIsLoading(false);
    }
  }, [selectedChapter]);

  useEffect(() => {
    loadQuestions();
  }, [loadQuestions]);

  const handleSelectAnswer = (index: number) => {
    if (showExplanation) return;
    setSelectedAnswer(index);
  };

  const handleSubmit = async () => {
    if (selectedAnswer === null) return;
    
    const currentQuestion = questions[currentIndex];
    const isCorrect = selectedAnswer === currentQuestion.correctAnswer;
    
    setAnswers(prev => [...prev, { 
      questionId: currentQuestion.id, 
      correct: isCorrect, 
      selected: selectedAnswer 
    }]);
    
    setShowExplanation(true);
  };

  const handleNext = () => {
    if (currentIndex < questions.length - 1) {
      setCurrentIndex(prev => prev + 1);
      setSelectedAnswer(null);
      setShowExplanation(false);
    } else {
      completeQuiz();
    }
  };

  const completeQuiz = async () => {
    const correctCount = answers.filter(a => a.correct).length;
    const totalTime = Math.floor((Date.now() - startTime) / 1000);
    
    const quizResult: QuizResult = {
      id: crypto.randomUUID(),
      date: new Date(),
      chapterId: selectedChapter?.id || 'mixed',
      score: Math.round((correctCount / questions.length) * 100),
      totalQuestions: questions.length,
      correctAnswers: correctCount,
      timeSpent: totalTime,
      answers: answers.map(a => ({
        questionId: a.questionId,
        selectedAnswer: a.selected,
        correct: a.correct,
        timeSpent: Math.floor(totalTime / questions.length),
      })),
      weakAreas: [],
      conceptMastery: [],
    };
    
    await db.quizResults.add(quizResult);
    setQuizComplete(true);
  };

  const handleRestart = () => {
    loadQuestions();
    setQuizComplete(false);
  };

  if (isLoading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="glass rounded-2xl p-8 text-center">
          <Brain className="w-12 h-12 mx-auto mb-3 text-violet-400 animate-pulse" />
          <p className="text-slate-400">Loading questions...</p>
        </div>
      </div>
    );
  }

  if (quizComplete) {
    const correctCount = answers.filter(a => a.correct).length;
    return (
      <QuizComplete 
        score={Math.round((correctCount / questions.length) * 100)}
        correct={correctCount}
        total={questions.length}
        timeSpent={Math.floor((Date.now() - startTime) / 1000)}
        onRestart={handleRestart}
      />
    );
  }

  if (questions.length === 0) {
    return (
      <div className="glass rounded-2xl p-8 text-center">
        <Brain className="w-16 h-16 mx-auto mb-4 text-slate-500" />
        <h3 className="text-xl font-semibold text-white mb-2">No Questions Yet</h3>
        <p className="text-slate-400">Upload a PDF to generate practice questions.</p>
      </div>
    );
  }

  const currentQuestion = questions[currentIndex];
  const progress = ((currentIndex + 1) / questions.length) * 100;

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center justify-between">
        <div>
          <h2 className="text-xl font-bold text-white">Practice Quiz</h2>
          <p className="text-sm text-slate-400">Question {currentIndex + 1} of {questions.length}</p>
        </div>
        
        <button
          onClick={() => setEli5Mode(!eli5Mode)}
          className={`flex items-center gap-2 px-4 py-2 rounded-xl transition-all ${
            eli5Mode 
              ? 'bg-emerald-500/20 text-emerald-400 border border-emerald-500/30' 
              : 'glass text-slate-400'
          }`}
        >
          <Sparkles className="w-4 h-4" />
          {eli5Mode ? 'ELI5 Mode' : 'Standard'}
        </button>
      </div>

      {/* Progress Bar */}
      <div className="glass rounded-full h-2 overflow-hidden">
        <div 
          className="h-full bg-gradient-to-r from-violet-500 to-fuchsia-500 transition-all duration-300"
          style={{ width: `${progress}%` }}
        />
      </div>

      {/* Question Card */}
      <div className="glass rounded-2xl p-6 sm:p-8">
        {/* Question */}
        <div className="mb-6">
          <div className="flex items-center gap-2 mb-4">
            <span className={`px-3 py-1 text-xs rounded-full ${
              currentQuestion.difficulty === 'easy' ? 'bg-emerald-500/20 text-emerald-400' :
              currentQuestion.difficulty === 'medium' ? 'bg-yellow-500/20 text-yellow-400' :
              'bg-red-500/20 text-red-400'
            }`}>
              {currentQuestion.difficulty}
            </span>
            <span className="px-3 py-1 text-xs bg-white/5 rounded-full text-slate-400">
              {currentQuestion.topic}
            </span>
          </div>
          
          <h3 className="text-xl sm:text-2xl font-semibold text-white">
            {currentQuestion.question}
          </h3>
        </div>

        {/* Options */}
        <div className="space-y-3">
          {currentQuestion.options.map((option, index) => {
            let buttonClass = 'glass w-full p-4 rounded-xl text-left transition-all flex items-center gap-3 ';
            
            if (showExplanation) {
              if (index === currentQuestion.correctAnswer) {
                buttonClass += 'bg-emerald-500/20 border-emerald-500/50 text-emerald-100';
              } else if (index === selectedAnswer) {
                buttonClass += 'bg-red-500/20 border-red-500/50 text-red-100';
              } else {
                buttonClass += 'opacity-50';
              }
            } else {
              if (selectedAnswer === index) {
                buttonClass += 'bg-violet-500/20 border-violet-500/50 text-violet-100 ring-2 ring-violet-500';
              } else {
                buttonClass += 'hover:bg-white/5 text-slate-200';
              }
            }
            
            return (
              <button
                key={index}
                onClick={() => handleSelectAnswer(index)}
                disabled={showExplanation}
                className={buttonClass}
              >
                <span className={`w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0 text-sm font-semibold ${
                  showExplanation 
                    ? index === currentQuestion.correctAnswer 
                      ? 'bg-emerald-500 text-white'
                      : index === selectedAnswer 
                        ? 'bg-red-500 text-white'
                        : 'bg-white/10 text-slate-400'
                    : selectedAnswer === index
                      ? 'bg-violet-500 text-white'
                      : 'bg-white/10 text-slate-400'
                }`}>
                  {String.fromCharCode(65 + index)}
                </span>
                <span>{option}</span>
                
                {showExplanation && index === currentQuestion.correctAnswer && (
                  <CheckCircle className="w-5 h-5 text-emerald-400 ml-auto" />
                )}
                {showExplanation && index === selectedAnswer && index !== currentQuestion.correctAnswer && (
                  <XCircle className="w-5 h-5 text-red-400 ml-auto" />
                )}
              </button>
            );
          })}
        </div>

        {/* Explanation */}
        {showExplanation && (
          <div className="mt-6 space-y-4">
            <div className={`rounded-xl p-5 ${
              selectedAnswer === currentQuestion.correctAnswer
                ? 'bg-emerald-500/10 border border-emerald-500/30'
                : 'bg-yellow-500/10 border border-yellow-500/30'
            }`}>
              <div className="flex items-center gap-2 mb-3">
                {selectedAnswer === currentQuestion.correctAnswer ? (
                  <>
                    <CheckCircle className="w-5 h-5 text-emerald-400" />
                    <span className="text-emerald-400 font-medium">Correct! </span>
                  </>
                ) : (
                  <>
                    <Lightbulb className="w-5 h-5 text-yellow-400" />
                    <span className="text-yellow-400 font-medium">Learning Moment</span>
                  </>
                )}
              </div>
              
              {eli5Mode && currentQuestion.eli5Explanation ? (
                <div className="space-y-3">
                  <p className="text-emerald-100">{currentQuestion.eli5Explanation}</p>
                  
                  <div className="glass rounded-lg p-4">
                    <p className="text-sm text-violet-300 font-medium mb-1"> Why this is correct:</p>
                    <p className="text-slate-300 text-sm">{currentQuestion.whyCorrect || currentQuestion.explanation}</p>
                  </div>
                </div>
              ) : (
                <p className="text-slate-300">{currentQuestion.explanation}</p>
              )}
            </div>
          </div>
        )}

        {/* Action Button */}
        <div className="mt-6">
          {!showExplanation ? (
            <button
              onClick={handleSubmit}
              disabled={selectedAnswer === null}
              className="w-full py-4 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 disabled:opacity-50 disabled:cursor-not-allowed text-white font-semibold rounded-xl transition-all flex items-center justify-center gap-2"
            >
              Submit Answer
            </button>
          ) : (
            <button
              onClick={handleNext}
              className="w-full py-4 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-semibold rounded-xl transition-all flex items-center justify-center gap-2"
            >
              {currentIndex < questions.length - 1 ? 'Next Question' : 'Finish Quiz'}
              <ArrowRight className="w-5 h-5" />
            </button>
          )}
        </div>
      </div>
    </div>
  );
}

function QuizComplete({ score, correct, total, timeSpent, onRestart }: { 
  score: number; 
  correct: number; 
  total: number;
  timeSpent: number;
  onRestart: () => void;
}) {
  const getMessage = () => {
    if (score >= 90) return { text: 'Outstanding! ', color: 'text-emerald-400', bg: 'from-emerald-500 to-teal-500' };
    if (score >= 70) return { text: 'Great job! ', color: 'text-violet-400', bg: 'from-violet-500 to-fuchsia-500' };
    if (score >= 50) return { text: 'Good effort! ', color: 'text-yellow-400', bg: 'from-yellow-500 to-orange-500' };
    return { text: 'Keep practicing! ', color: 'text-orange-400', bg: 'from-orange-500 to-red-500' };
  };
  
  const message = getMessage();
  const minutes = Math.floor(timeSpent / 60);
  const seconds = timeSpent % 60;
  
  return (
    <div className="glass rounded-2xl p-8 text-center">
      <div className={`w-24 h-24 mx-auto mb-6 rounded-2xl bg-gradient-to-br ${message.bg} flex items-center justify-center shadow-lg`}>
        <Trophy className="w-12 h-12 text-white" />
      </div>
      
      <h3 className={`text-2xl font-bold mb-2 ${message.color}`}>{message.text}</h3>
      <p className="text-slate-400 mb-6">Quiz Complete!</p>
      
      <div className="grid grid-cols-2 md:grid-cols-4 gap-4 mb-8">
        <div className="glass rounded-xl p-4">
          <p className="text-3xl font-bold text-violet-400">{score}%</p>
          <p className="text-sm text-slate-500">Score</p>
        </div>
        <div className="glass rounded-xl p-4">
          <p className="text-3xl font-bold text-emerald-400">{correct}</p>
          <p className="text-sm text-slate-500">Correct</p>
        </div>
        <div className="glass rounded-xl p-4">
          <p className="text-3xl font-bold text-red-400">{total - correct}</p>
          <p className="text-sm text-slate-500">Incorrect</p>
        </div>
        <div className="glass rounded-xl p-4">
          <p className="text-3xl font-bold text-amber-400">{minutes}:{seconds.toString().padStart(2, '0')}</p>
          <p className="text-sm text-slate-500">Time</p>
        </div>
      </div>
      
      <button
        onClick={onRestart}
        className="px-8 py-4 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-semibold rounded-xl transition-all flex items-center justify-center gap-2 mx-auto"
      >
        <RotateCcw className="w-5 h-5" />
        Take Another Quiz
      </button>
    </div>
  );
}
