import { useState } from 'react';
import { 
  ArrowLeft, BookOpen, Lightbulb, HelpCircle, Brain, 
  FileText, CheckCircle, Sparkles, ChevronRight,
  Home, DollarSign, Scale, Users, Building, FileCheck
} from 'lucide-react';
import type { Chapter, Concept } from '../types';

interface ChapterDetailProps {
  chapter: Chapter;
  onBack: () => void;
  onStudyConcept: (concept: Concept) => void;
  onTakeQuiz: () => void;
}

const iconMap: Record<string, React.ComponentType<{ className?: string }>> = {
  Home, DollarSign, Scale, Users, Building, FileCheck,
  BookOpen, Lightbulb, Brain
};

export default function ChapterDetail({ chapter, onBack, onStudyConcept, onTakeQuiz }: ChapterDetailProps) {
  const [activeTab, setActiveTab] = useState<'overview' | 'concepts' | 'visuals'>('overview');
  const [selectedConcept, setSelectedConcept] = useState<Concept | null>(null);
  const [eli5Mode, setEli5Mode] = useState(true);

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="flex items-center gap-4">
        <button
          onClick={onBack}
          className="p-2 glass rounded-xl hover:bg-white/10 transition-colors"
        >
          <ArrowLeft className="w-5 h-5 text-slate-300" />
        </button>
        <div className="flex-1">
          <h1 className="text-xl font-bold text-white">{chapter.title}</h1>
          <p className="text-sm text-slate-400">Chapter {chapter.number}</p>
        </div>
      </div>

      {/* ELI5 Toggle */}
      <div className="glass rounded-xl p-1 flex gap-1">
        {[
          { key: true, label: ' Simple Mode (ELI5)', desc: 'Easy explanations with analogies' },
          { key: false, label: ' Advanced Mode', desc: 'Detailed technical content' },
        ].map((mode) => (
          <button
            key={String(mode.key)}
            onClick={() => setEli5Mode(mode.key as boolean)}
            className={`flex-1 p-3 rounded-lg text-left transition-all ${
              eli5Mode === mode.key
                ? 'bg-gradient-to-r from-violet-500/20 to-fuchsia-500/20 border border-violet-500/30'
                : 'hover:bg-white/5'
            }`}
          >
            <p className={`font-medium ${eli5Mode === mode.key ? 'text-violet-300' : 'text-slate-400'}`}>
              {mode.label}
            </p>
            <p className="text-xs text-slate-500">{mode.desc}</p>
          </button>
        ))}
      </div>

      {/* Tabs */}
      <div className="flex gap-2">
        {[
          { key: 'overview', label: 'Overview', icon: BookOpen },
          { key: 'concepts', label: `Concepts (${chapter.concepts?.length || 0})`, icon: Lightbulb },
          { key: 'visuals', label: 'Visual Aids', icon: Brain },
        ].map((tab) => (
          <button
            key={tab.key}
            onClick={() => setActiveTab(tab.key as any)}
            className={`flex items-center gap-2 px-4 py-2 rounded-xl transition-all ${
              activeTab === tab.key
                ? 'bg-violet-500/20 text-violet-300 border border-violet-500/30'
                : 'glass text-slate-400 hover:text-slate-200'
            }`}
          >
            <tab.icon className="w-4 h-4" />
            {tab.label}
          </button>
        ))}
      </div>

      {/* Content */}
      <div className="glass rounded-2xl p-6">
        {activeTab === 'overview' && (
          <OverviewTab chapter={chapter} eli5Mode={eli5Mode} onTakeQuiz={onTakeQuiz} />
        )}
        {activeTab === 'concepts' && (
          <ConceptsTab 
            chapter={chapter} 
            eli5Mode={eli5Mode}
            selectedConcept={selectedConcept}
            onSelectConcept={setSelectedConcept}
            onStudyConcept={onStudyConcept}
          />
        )}
        {activeTab === 'visuals' && (
          <VisualsTab chapter={chapter} />
        )}
      </div>
    </div>
  );
}

function OverviewTab({ chapter, eli5Mode, onTakeQuiz }: { 
  chapter: Chapter; 
  eli5Mode: boolean;
  onTakeQuiz: () => void;
}) {
  return (
    <div className="space-y-6">
      {eli5Mode && chapter.eli5Summary ? (
        <div className="space-y-4">
          <div className="bg-gradient-to-r from-emerald-500/10 to-teal-500/10 border border-emerald-500/30 rounded-xl p-6">
            <div className="flex items-center gap-2 mb-3">
              <Sparkles className="w-5 h-5 text-emerald-400" />
              <span className="text-emerald-400 font-medium">In One Sentence</span>
            </div>
            <p className="text-lg text-emerald-100">{chapter.eli5Summary.oneSentence}</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="glass rounded-xl p-5">
              <div className="flex items-center gap-2 mb-3">
                <BookOpen className="w-5 h-5 text-violet-400" />
                <span className="text-violet-300 font-medium">What This Means</span>
              </div>
              <p className="text-slate-300">{chapter.eli5Summary.shortSummary}</p>
            </div>

            <div className="glass rounded-xl p-5">
              <div className="flex items-center gap-2 mb-3">
                <HelpCircle className="w-5 h-5 text-fuchsia-400" />
                <span className="text-fuchsia-300 font-medium">Why It Matters</span>
              </div>
              <p className="text-slate-300">{chapter.eli5Summary.whyItMatters}</p>
            </div>
          </div>

          <div className="glass rounded-xl p-5">
            <div className="flex items-center gap-2 mb-3">
              <CheckCircle className="w-5 h-5 text-amber-400" />
              <span className="text-amber-300 font-medium">Real World Connection</span>
            </div>
            <p className="text-slate-300">{chapter.eli5Summary.realWorldConnection}</p>
          </div>
        </div>
      ) : (
        <div className="space-y-4">
          <div className="prose prose-invert max-w-none">
            <h3 className="text-white font-semibold">Chapter Summary</h3>
            <p className="text-slate-300">{chapter.summary || 'No summary available.'}</p>
            
            {chapter.keyTerms && chapter.keyTerms.length > 0 && (
              <>
                <h3 className="text-white font-semibold mt-6">Key Terms</h3>
                <dl className="space-y-3">
                  {chapter.keyTerms.map((term, i) => (
                    <div key={i}>
                      <dt className="text-violet-300 font-medium">{term.term}</dt>
                      <dd className="text-slate-400">{term.definition}</dd>
                    </div>
                  ))}
                </dl>
              </>
            )}
          </div>
        </div>
      )}

      <button
        onClick={onTakeQuiz}
        className="w-full py-4 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-semibold rounded-xl transition-all flex items-center justify-center gap-2 shadow-lg shadow-violet-500/25"
      >
        <Brain className="w-5 h-5" />
        Take Practice Quiz
      </button>
    </div>
  );
}

function ConceptsTab({ 
  chapter, 
  eli5Mode, 
  selectedConcept, 
  onSelectConcept,
  onStudyConcept 
}: { 
  chapter: Chapter; 
  eli5Mode: boolean;
  selectedConcept: Concept | null;
  onSelectConcept: (c: Concept | null) => void;
  onStudyConcept: (c: Concept) => void;
}) {
  const concepts = chapter.concepts || [];

  if (concepts.length === 0) {
    return (
      <div className="text-center py-8">
        <Lightbulb className="w-12 h-12 mx-auto mb-3 text-slate-500" />
        <p className="text-slate-400">No concepts extracted from this chapter.</p>
      </div>
    );
  }

  if (selectedConcept) {
    return (
      <ConceptDetail 
        concept={selectedConcept} 
        eli5Mode={eli5Mode}
        onBack={() => onSelectConcept(null)}
        onStudy={() => onStudyConcept(selectedConcept)}
      />
    );
  }

  return (
    <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
      {concepts.map((concept) => {
        const Icon = iconMap[concept.icon] || Lightbulb;
        return (
          <button
            key={concept.id}
            onClick={() => onSelectConcept(concept)}
            className="glass rounded-xl p-5 text-left hover:bg-white/5 transition-all group"
          >
            <div className="flex items-start gap-4">
              <div className={`w-10 h-10 rounded-xl flex items-center justify-center flex-shrink-0 bg-${concept.color}-500/20`}>
                <Icon className={`w-5 h-5 text-${concept.color}-400`} />
              </div>
              <div className="flex-1">
                <h4 className="font-semibold text-white group-hover:text-violet-300 transition-colors">
                  {concept.name}
                </h4>
                <p className="text-slate-400 text-sm mt-1 line-clamp-2">
                  {eli5Mode ? concept.simpleExplanation : concept.detailedExplanation}
                </p>
                <div className="flex items-center gap-2 mt-3">
                  <span className={`px-2 py-0.5 text-xs rounded-full ${
                    concept.difficulty === 'beginner' ? 'bg-emerald-500/20 text-emerald-400' :
                    concept.difficulty === 'intermediate' ? 'bg-yellow-500/20 text-yellow-400' :
                    'bg-red-500/20 text-red-400'
                  }`}>
                    {concept.difficulty}
                  </span>
                  <span className="text-xs text-slate-500">Click to learn more </span>
                </div>
              </div>
            </div>
          </button>
        );
      })}
    </div>
  );
}

function ConceptDetail({ concept, eli5Mode, onBack, onStudy }: { 
  concept: Concept; 
  eli5Mode: boolean;
  onBack: () => void;
  onStudy: () => void;
}) {
  const Icon = iconMap[concept.icon] || Lightbulb;

  return (
    <div className="space-y-6">
      <button
        onClick={onBack}
        className="flex items-center gap-2 text-slate-400 hover:text-white transition-colors"
      >
        <ChevronRight className="w-4 h-4 rotate-180" />
        Back to concepts
      </button>

      <div className="flex items-start gap-4">
        <div className={`w-14 h-14 rounded-2xl flex items-center justify-center flex-shrink-0 bg-${concept.color}-500/20`}>
          <Icon className={`w-7 h-7 text-${concept.color}-400`} />
        </div>
        <div>
          <h3 className="text-2xl font-bold text-white">{concept.name}</h3>
          <div className="flex items-center gap-2 mt-2">
            <span className={`px-2 py-1 text-xs rounded-full ${
              concept.difficulty === 'beginner' ? 'bg-emerald-500/20 text-emerald-400' :
              concept.difficulty === 'intermediate' ? 'bg-yellow-500/20 text-yellow-400' :
              'bg-red-500/20 text-red-400'
            }`}>
              {concept.difficulty}
            </span>
            {concept.tags.map((tag, i) => (
              <span key={i} className="px-2 py-1 text-xs bg-white/5 rounded-full text-slate-400">
                {tag}
              </span>
            ))}
          </div>
        </div>
      </div>

      {eli5Mode ? (
        <div className="space-y-4">
          <div className="bg-emerald-500/10 border border-emerald-500/30 rounded-xl p-5">
            <div className="flex items-center gap-2 mb-3">
              <Sparkles className="w-5 h-5 text-emerald-400" />
              <span className="text-emerald-400 font-medium">Simple Explanation</span>
            </div>
            <p className="text-emerald-100 text-lg">{concept.simpleExplanation}</p>
          </div>

          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="glass rounded-xl p-5">
              <div className="flex items-center gap-2 mb-3">
                <BookOpen className="w-5 h-5 text-violet-400" />
                <span className="text-violet-300 font-medium">Analogy</span>
              </div>
              <p className="text-slate-300 italic">"{concept.analogy}"</p>
            </div>

            <div className="glass rounded-xl p-5">
              <div className="flex items-center gap-2 mb-3">
                <CheckCircle className="w-5 h-5 text-amber-400" />
                <span className="text-amber-300 font-medium">Real World Example</span>
              </div>
              <p className="text-slate-300">{concept.realWorldExample}</p>
            </div>
          </div>
        </div>
      ) : (
        <div className="glass rounded-xl p-5">
          <h4 className="text-violet-300 font-medium mb-3">Detailed Explanation</h4>
          <p className="text-slate-300">{concept.detailedExplanation}</p>
          
          {concept.visualDescription && (
            <>
              <h4 className="text-violet-300 font-medium mt-4 mb-3">Visual Description</h4>
              <p className="text-slate-400">{concept.visualDescription}</p>
            </>
          )}
        </div>
      )}

      <button
        onClick={onStudy}
        className="w-full py-4 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 text-white font-semibold rounded-xl transition-all flex items-center justify-center gap-2"
      >
        <Brain className="w-5 h-5" />
        Study This Concept
      </button>
    </div>
  );
}

function VisualsTab({ chapter }: { chapter: Chapter }) {
  const visualAids = chapter.visualAids || [];

  if (visualAids.length === 0) {
    return (
      <div className="text-center py-8">
        <Brain className="w-12 h-12 mx-auto mb-3 text-slate-500" />
        <p className="text-slate-400">No visual aids available for this chapter.</p>
      </div>
    );
  }

  return (
    <div className="space-y-4">
      {visualAids.map((aid) => (
        <div key={aid.id} className="glass rounded-xl p-5">
          <div className="flex items-center gap-2 mb-3">
            <FileText className="w-5 h-5 text-violet-400" />
            <h4 className="font-semibold text-white">{aid.title}</h4>
          </div>
          <p className="text-slate-400 text-sm mb-4">{aid.description}</p>
          
          <div className="bg-slate-950/50 rounded-lg p-4 border border-white/5">
            <pre className="text-xs text-slate-400 overflow-auto">
              {JSON.stringify(aid.data, null, 2)}
            </pre>
          </div>
        </div>
      ))}
    </div>
  );
}
