// PDF utilities with proper pdf.js integration
import * as pdfjsLib from 'pdfjs-dist';

// Set worker to local file
pdfjsLib.GlobalWorkerOptions.workerSrc = '/pdf.worker.min.js';

export interface PDFChapter {
  number: number;
  title: string;
  content: string;
  startPage: number;
  endPage: number;
}

export async function extractTextFromPDF(file: File): Promise<string> {
  const result = await extractTextFromPDFWithChapters(file);
  return result.text;
}

export async function extractTextFromPDFWithChapters(file: File): Promise<{
  text: string;
  numPages: number;
  chapters: PDFChapter[];
}> {
  try {
    const arrayBuffer = await file.arrayBuffer();
    const pdf = await pdfjsLib.getDocument({ data: arrayBuffer }).promise;
    const numPages = pdf.numPages;

    let fullText = '';
    const pageTexts: string[] = [];

    // Extract text from all pages
    for (let i = 1; i <= numPages; i++) {
      const page = await pdf.getPage(i);
      const textContent = await page.getTextContent();
      const pageText = textContent.items
        .map((item: any) => item.str)
        .join(' ')
        .replace(/\s+/g, ' ')
        .trim();
      
      pageTexts.push(pageText);
      fullText += pageText + '\n\n';
    }

    // Detect chapters
    const chapters = detectChapters(pageTexts);

    return {
      text: fullText,
      numPages,
      chapters,
    };
  } catch (error) {
    console.error('PDF extraction error:', error);
    throw new Error('Failed to extract text from PDF. The file may be corrupted or password-protected.');
  }
}

function detectChapters(pageTexts: string[]): PDFChapter[] {
  const chapters: PDFChapter[] = [];
  const chapterPatterns = [
    /Chapter\s+(\d+)[:\s]+(.+?)(?=\n|$)/i,
    /CHAPTER\s+(\d+)[:\s]+(.+?)(?=\n|$)/i,
    /^(\d+)\.\s+([A-Z][^.]+?)(?=\n|$)/m,
  ];

  let currentChapter: PDFChapter | null = null;
  let chapterContent = '';

  for (let i = 0; i < pageTexts.length; i++) {
    const pageText = pageTexts[i];
    let foundChapter = false;

    for (const pattern of chapterPatterns) {
      const match = pageText.match(pattern);
      if (match) {
        // Save previous chapter
        if (currentChapter) {
          currentChapter.content = chapterContent.trim();
          currentChapter.endPage = i;
          chapters.push(currentChapter);
        }

        // Start new chapter
        const chapterNum = parseInt(match[1], 10);

        currentChapter = {
          number: chapterNum || chapters.length + 1,
          title: match[2].trim(),
          content: '',
          startPage: i + 1,
          endPage: i + 1,
        };
        chapterContent = pageText.substring(match.index! + match[0].length);
        foundChapter = true;
        break;
      }
    }

    if (!foundChapter && currentChapter) {
      chapterContent += '\n\n' + pageText;
    }
  }

  // Save last chapter
  if (currentChapter) {
    currentChapter.content = chapterContent.trim();
    currentChapter.endPage = pageTexts.length;
    chapters.push(currentChapter);
  }

  // If no chapters detected, split into chunks
  if (chapters.length === 0) {
    const chunkSize = Math.ceil(pageTexts.length / 4);
    for (let i = 0; i < 4; i++) {
      const start = i * chunkSize;
      const end = Math.min((i + 1) * chunkSize, pageTexts.length);
      if (start < pageTexts.length) {
        chapters.push({
          number: i + 1,
          title: `Section ${i + 1}`,
          content: pageTexts.slice(start, end).join('\n\n'),
          startPage: start + 1,
          endPage: end,
        });
      }
    }
  }

  return chapters;
}

export function createMockPDFData(): {
  text: string;
  numPages: number;
  chapters: PDFChapter[];
} {
  const chapterContents = [
    {
      number: 1,
      title: 'Introduction to Real Estate',
      content: `Real estate is defined as land and everything permanently attached to it, including natural resources and man-made improvements. The real estate industry encompasses the buying, selling, leasing, and management of property.

Key Concepts:
Real property includes land plus all permanent attachments to land. Personal property refers to movable items not permanently attached to land. Fixtures are items that were once personal property but have been attached to real estate and are now considered part of the real property.

Types of Real Estate:
Residential real estate includes single-family homes, condominiums, townhouses, and multi-family properties. Commercial real estate encompasses office buildings, retail spaces, and shopping centers. Industrial real estate includes warehouses, manufacturing facilities, and distribution centers. Agricultural real estate consists of farms, ranches, and timberland.

Real Estate Market Fundamentals:
The real estate market operates on the principles of supply and demand. Location is the most important factor in determining property value. Economic conditions, interest rates, and demographic trends all influence real estate values and market activity.`,
    },
    {
      number: 2,
      title: 'Property Ownership and Rights',
      content: `Ownership of real estate includes a bundle of rights that can be separated and transferred individually. These rights include the right to possess, use, transfer, encumber, and exclude others from the property.

Forms of Ownership:
Sole ownership occurs when one person or entity owns the property entirely. Joint tenancy involves two or more owners with equal shares and the right of survivorship. Tenancy in common allows multiple owners with potentially unequal shares and no right of survivorship. Community property is a form of ownership available to married couples in certain states.

Property Rights and Limitations:
Surface rights include the right to use the surface of the land. Subsurface rights allow the owner to extract minerals, oil, and gas. Air rights govern the use of space above the property. Easements grant others limited use rights to the property. Covenants, conditions, and restrictions (CC&Rs) limit how property can be used.

Legal Descriptions:
Metes and bounds descriptions use measurements and directions to define property boundaries. The rectangular survey system divides land into townships, sections, and smaller units. Lot and block descriptions reference recorded plat maps that show subdivided lots.`,
    },
    {
      number: 3,
      title: 'Real Estate Finance',
      content: `Real estate finance involves the various methods of funding property purchases and developments. Understanding financing options is essential for real estate professionals and consumers alike.

Types of Loans:
Fixed-rate mortgages maintain the same interest rate throughout the loan term. Adjustable-rate mortgages (ARMs) have interest rates that can change periodically based on market conditions. FHA loans are government-insured loans with lower down payment requirements. VA loans are available to eligible veterans and active military personnel with favorable terms.

Key Formulas:
Loan-to-Value Ratio (LTV) = Loan Amount / Property Value. For example, if a buyer purchases a $300,000 home with a $60,000 down payment, the LTV is $240,000 / $300,000 = 80%.

Debt-to-Income Ratio (DTI) = Monthly Debt Payments / Gross Monthly Income. Lenders typically prefer a DTI of 43% or lower.

Monthly Mortgage Payment = P  (r(1+r)^n) / ((1+r)^n - 1), where P is principal, r is monthly interest rate, and n is number of payments.

Interest = Principal  Rate  Time. This simple interest formula is used for calculating interest on some real estate loans.

Amortization and Payments:
Amortization is the gradual repayment of a loan through regular payments. Early payments consist primarily of interest, with more principal paid as the loan matures. Points are upfront fees paid to reduce the interest rate, with each point equal to 1% of the loan amount.`,
    },
    {
      number: 4,
      title: 'Contracts and Agency',
      content: `Real estate transactions are governed by contracts that establish the rights and obligations of the parties involved. Agency relationships define the legal responsibilities between real estate professionals and their clients.

Essential Elements of a Contract:
A valid contract requires offer and acceptance, consideration, legally competent parties, legal purpose, and consent. Real estate contracts must be in writing to satisfy the Statute of Frauds. Consideration is something of value exchanged between parties.

Types of Real Estate Contracts:
Purchase agreements specify the terms of property sales. Listing agreements authorize brokers to sell or lease property. Buyer representation agreements establish agency relationships with buyers. Lease agreements govern landlord-tenant relationships.

Agency Relationships:
A seller's agent represents the seller's interests exclusively. A buyer's agent represents the buyer's interests exclusively. A dual agent represents both parties in the same transaction with informed consent. A transaction broker facilitates the transaction without representing either party.

Fiduciary Duties:
Agents owe clients the duties of loyalty, confidentiality, obedience, accounting, and disclosure. These duties require agents to act in the best interests of their clients while maintaining professional standards.`,
    },
  ];

  const chapters: PDFChapter[] = chapterContents.map((ch) => ({
    ...ch,
    startPage: ch.number,
    endPage: ch.number,
  }));

  return {
    text: chapterContents.map((ch) => `Chapter ${ch.number}: ${ch.title}\n\n${ch.content}`).join('\n\n---\n\n'),
    numPages: 4,
    chapters,
  };
}
