import { useState, useEffect } from 'react';
import { BookOpen, Brain, Calendar, BarChart3, Menu, X, Key, Sparkles, Home, TrendingUp, FileSearch } from 'lucide-react';
import PDFUpload from './PDFUpload';
import ChapterList from './ChapterList';
import ChapterDetail from './ChapterDetail';
import FlashcardView from './FlashcardView';
import QuizView from './QuizView';
import StudySchedule from './StudySchedule';
import ProgressDashboard from './ProgressDashboard';
import APIKeySetup from './APIKeySetup';
import BrainTestView from './BrainTest';
import { db } from '../db';
import { hasApiKey } from '../services/ai';
import type { Book, Chapter, Concept } from '../types';

type View = 'books' | 'chapter' | 'flashcards' | 'quiz' | 'schedule' | 'progress' | 'market-analysis' | 'comps' | 'brain-test';

export default function App() {
  const [currentView, setCurrentView] = useState<View>('books');
  const [books, setBooks] = useState<Book[]>([]);
  const [selectedBook, setSelectedBook] = useState<Book | null>(null);
  const [selectedChapter, setSelectedChapter] = useState<Chapter | null>(null);
  const [selectedConcept, setSelectedConcept] = useState<Concept | null>(null);
  const [chapters, setChapters] = useState<Chapter[]>([]);
  const [isMenuOpen, setIsMenuOpen] = useState(false);
  const [showApiKeySetup, setShowApiKeySetup] = useState(false);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    loadBooks();
    
    // Register service worker for PWA
    if ('serviceWorker' in navigator) {
      navigator.serviceWorker.register('/sw.js').catch(console.error);
    }

    // Check if API key is set
    const timer = setTimeout(() => {
      setShowApiKeySetup(!hasApiKey());
    }, 500);

    return () => clearTimeout(timer);
  }, []);

  const loadBooks = async () => {
    setIsLoading(true);
    const allBooks = await db.books.toArray();
    setBooks(allBooks);
    setIsLoading(false);
  };

  const loadChapters = async (bookId: string) => {
    setIsLoading(true);
    const bookChapters = await db.chapters.where('bookId').equals(bookId).sortBy('number');
    setChapters(bookChapters);
    setIsLoading(false);
  };

  const handleBookSelect = async (book: Book) => {
    setSelectedBook(book);
    await loadChapters(book.id);
    setCurrentView('books');
    setSelectedChapter(null);
  };

  const handleChapterSelect = (chapter: Chapter) => {
    setSelectedChapter(chapter);
    setCurrentView('chapter');
  };

  const handleUploadComplete = async () => {
    await loadBooks();
    const latestBook = await db.books.orderBy('uploadedAt').last();
    if (latestBook) {
      handleBookSelect(latestBook);
    }
  };

  const handleApiKeyComplete = () => setShowApiKeySetup(false);
  const handleApiKeySkip = () => setShowApiKeySetup(false);
  const openApiKeySettings = () => { setShowApiKeySetup(true); setIsMenuOpen(false); };

  const handleStudyConcept = (concept: Concept) => {
    setSelectedConcept(concept);
    setCurrentView('flashcards');
  };

  const renderView = () => {
    switch (currentView) {
      case 'books':
        return selectedBook ? (
          <ChapterList 
            book={selectedBook} 
            chapters={chapters}
            onChapterSelect={handleChapterSelect}
            onChaptersUpdated={loadChapters}
          />
        ) : (
          <PDFUpload onUploadComplete={handleUploadComplete} />
        );
      case 'chapter':
        return selectedChapter ? (
          <ChapterDetail 
            chapter={selectedChapter}
            onBack={() => setCurrentView('books')}
            onStudyConcept={handleStudyConcept}
            onTakeQuiz={() => setCurrentView('quiz')}
          />
        ) : (
          <PDFUpload onUploadComplete={handleUploadComplete} />
        );
      case 'flashcards':
        return <FlashcardView initialConcept={selectedConcept} />;
      case 'quiz':
        return <QuizView chapters={chapters} selectedChapter={selectedChapter} />;
      case 'schedule':
        return <StudySchedule />;
      case 'progress':
        return <ProgressDashboard bookId={selectedBook?.id} />;
      case 'market-analysis':
        return <MarketAnalysisView />;
      case 'comps':
        return <CompsReportView />;
      case 'brain-test':
        return <BrainTestView />;
      default:
        return <PDFUpload onUploadComplete={handleUploadComplete} />;
    }
  };

  // Show API key setup on first load if no key is set
  if (showApiKeySetup) {
    return (
      <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950">
        <APIKeySetup onComplete={handleApiKeyComplete} onSkip={handleApiKeySkip} />
      </div>
    );
  }

  const navItems = [
    { view: 'books', label: 'Study Material', icon: BookOpen },
    { view: 'flashcards', label: 'Flashcards', icon: Brain },
    { view: 'quiz', label: 'Practice Quiz', icon: FileSearch },
    { view: 'schedule', label: 'Study Plan', icon: Calendar },
    { view: 'progress', label: 'Progress', icon: BarChart3 },
  ];

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-950 via-slate-900 to-slate-950 text-slate-100">
      {/* Header */}
      <header className="sticky top-0 z-50 glass border-b border-white/10">
        <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
          <div className="flex items-center justify-between h-16">
            <div className="flex items-center gap-4">
              <button
                onClick={() => setIsMenuOpen(!isMenuOpen)}
                className="lg:hidden p-2 rounded-xl glass hover:bg-white/10 transition-colors"
              >
                {isMenuOpen ? <X className="w-5 h-5" /> : <Menu className="w-5 h-5" />}
              </button>
              
              <div className="flex items-center gap-3">
                <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center shadow-lg shadow-violet-500/25">
                  <Sparkles className="w-5 h-5 text-white" />
                </div>
                <div>
                  <h1 className="text-lg font-bold bg-gradient-to-r from-white to-slate-400 bg-clip-text text-transparent">
                    RE Study AI
                  </h1>
                  <p className="text-xs text-slate-400">Deep Learning Assistant</p>
                </div>
              </div>
            </div>

            <div className="flex items-center gap-3">
              {books.length > 0 && (
                <select
                  value={selectedBook?.id || ''}
                  onChange={(e) => {
                    const book = books.find(b => b.id === e.target.value);
                    if (book) handleBookSelect(book);
                  }}
                  className="hidden sm:block text-sm glass border border-white/10 rounded-xl px-4 py-2 text-slate-200 focus:ring-2 focus:ring-violet-500/50 focus:border-transparent min-w-[200px]"
                >
                  <option value="" className="bg-slate-900">Select material...</option>
                  {books.map(book => (
                    <option key={book.id} value={book.id} className="bg-slate-900">{book.title}</option>
                  ))}
                </select>
              )}
              
              <button
                onClick={openApiKeySettings}
                className={`p-2 rounded-xl glass hover:bg-white/10 transition-colors ${hasApiKey() ? 'text-emerald-400' : 'text-amber-400'}`}
                title="API Key Settings"
              >
                <Key className="w-5 h-5" />
              </button>
            </div>
          </div>
        </div>
      </header>

      <div className="flex">
        {/* Sidebar Navigation */}
        <aside
          className={`fixed inset-y-0 left-0 z-40 w-72 glass-dark border-r border-white/10 transform transition-transform duration-300 ease-out lg:translate-x-0 lg:static lg:h-[calc(100vh-4rem)] ${
            isMenuOpen ? 'translate-x-0' : '-translate-x-full'
          }`}
        >
          <nav className="p-4 space-y-2">
            {navItems.map((item) => {
              const Icon = item.icon;
              const isActive = currentView === item.view || (item.view === 'books' && currentView === 'chapter');
              return (
                <button
                  key={item.view}
                  onClick={() => { setCurrentView(item.view as View); setIsMenuOpen(false); }}
                  className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${
                    isActive
                      ? 'bg-gradient-to-r from-violet-500/20 to-fuchsia-500/20 text-violet-300 border border-violet-500/30'
                      : 'text-slate-400 hover:bg-white/5 hover:text-slate-200'
                  }`}
                >
                  <Icon className={`w-5 h-5 ${isActive ? 'text-violet-400' : ''}`} />
                  {item.label}
                </button>
              );
            })}
            
            {/* AI Tools Section */}
            <div className="pt-4 mt-4 border-t border-white/10">
              <p className="px-4 text-xs font-semibold text-slate-500 uppercase tracking-wider mb-3">
                AI Tools
              </p>
              <button
                onClick={() => { setCurrentView('market-analysis'); setIsMenuOpen(false); }}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${
                  currentView === 'market-analysis'
                    ? 'bg-gradient-to-r from-emerald-500/20 to-teal-500/20 text-emerald-300 border border-emerald-500/30'
                    : 'text-slate-400 hover:bg-white/5 hover:text-slate-200'
                }`}
              >
                <TrendingUp className="w-5 h-5" />
                Market Analysis
              </button>
              <button
                onClick={() => { setCurrentView('comps'); setIsMenuOpen(false); }}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${
                  currentView === 'comps'
                    ? 'bg-gradient-to-r from-emerald-500/20 to-teal-500/20 text-emerald-300 border border-emerald-500/30'
                    : 'text-slate-400 hover:bg-white/5 hover:text-slate-200'
                }`}
              >
                <Home className="w-5 h-5" />
                Comps Report
              </button>
              <button
                onClick={() => { setCurrentView('brain-test'); setIsMenuOpen(false); }}
                className={`w-full flex items-center gap-3 px-4 py-3 rounded-xl text-sm font-medium transition-all duration-200 ${
                  currentView === 'brain-test'
                    ? 'bg-gradient-to-r from-violet-500/20 to-fuchsia-500/20 text-violet-300 border border-violet-500/30'
                    : 'text-slate-400 hover:bg-white/5 hover:text-slate-200'
                }`}
              >
                <Brain className="w-5 h-5" />
                Brain Test 
              </button>
            </div>
          </nav>

          <div className="absolute bottom-0 left-0 right-0 p-4 border-t border-white/10">
            <div className="glass rounded-xl p-3 space-y-2">
              <div className="flex items-center gap-2 text-xs text-slate-400">
                <span className={`w-2 h-2 rounded-full ${hasApiKey() ? 'bg-emerald-400 animate-pulse' : 'bg-amber-400'}`} />
                {hasApiKey() ? 'AI Connected' : 'Offline Mode'}
              </div>
              <p className="text-xs text-slate-500">Real Estate Study Assistant v2.0</p>
            </div>
          </div>
        </aside>

        {/* Overlay for mobile */}
        {isMenuOpen && (
          <div className="fixed inset-0 bg-black/60 backdrop-blur-sm z-30 lg:hidden" onClick={() => setIsMenuOpen(false)} />
        )}

        {/* Main Content */}
        <main className="flex-1 min-h-[calc(100vh-4rem)] p-4 sm:p-6 lg:p-8">
          <div className="max-w-6xl mx-auto">
            {isLoading ? (
              <div className="flex items-center justify-center h-64">
                <div className="glass rounded-2xl p-8 flex flex-col items-center gap-4">
                  <div className="w-12 h-12 border-4 border-violet-500/30 border-t-violet-500 rounded-full animate-spin" />
                  <p className="text-slate-400">Loading your study material...</p>
                </div>
              </div>
            ) : (
              renderView()
            )}
          </div>
        </main>
      </div>
    </div>
  );
}

// Market Analysis View Component
function MarketAnalysisView() {
  const [location, setLocation] = useState('');
  const [propertyType, setPropertyType] = useState('');
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  const handleAnalyze = async () => {
    if (!location.trim()) return;
    setLoading(true);
    setError(null);
    
    try {
      const { getMarketAnalysis } = await import('../services/ai');
      const analysis = await getMarketAnalysis(location, propertyType);
      setResult(analysis);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to analyze market');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <div className="glass rounded-2xl p-6">
        <h2 className="text-2xl font-bold text-white mb-2">Market Analysis</h2>
        <p className="text-slate-400 mb-6">Get AI-powered market insights for any location</p>
        
        <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mb-6">
          <div className="md:col-span-2">
            <label className="block text-sm font-medium text-slate-300 mb-2">Location</label>
            <input
              type="text"
              value={location}
              onChange={(e) => setLocation(e.target.value)}
              placeholder="e.g., Austin, TX or Miami-Dade County"
              className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white placeholder-slate-500 focus:ring-2 focus:ring-violet-500/50 focus:border-transparent"
            />
          </div>
          <div>
            <label className="block text-sm font-medium text-slate-300 mb-2">Property Type</label>
            <select
              value={propertyType}
              onChange={(e) => setPropertyType(e.target.value)}
              className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white focus:ring-2 focus:ring-violet-500/50 focus:border-transparent"
            >
              <option value="" className="bg-slate-900">All Types</option>
              <option value="single-family" className="bg-slate-900">Single Family</option>
              <option value="condo" className="bg-slate-900">Condo</option>
              <option value="townhouse" className="bg-slate-900">Townhouse</option>
              <option value="multi-family" className="bg-slate-900">Multi-Family</option>
            </select>
          </div>
        </div>
        
        <button
          onClick={handleAnalyze}
          disabled={loading || !location.trim()}
          className="px-6 py-3 bg-gradient-to-r from-violet-500 to-fuchsia-500 hover:from-violet-400 hover:to-fuchsia-400 disabled:opacity-50 disabled:cursor-not-allowed text-white font-medium rounded-xl transition-all flex items-center gap-2 shadow-lg shadow-violet-500/25"
        >
          {loading ? <><div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" /> Analyzing...</> : 'Analyze Market'}
        </button>
        
        {error && (
          <div className="mt-4 p-4 bg-red-500/10 border border-red-500/30 rounded-xl text-red-300">
            {error}
          </div>
        )}
      </div>

      {result && (
        <div className="glass rounded-2xl p-6 space-y-6">
          <div>
            <h3 className="text-xl font-semibold text-white mb-2">Market Insights</h3>
            <p className="text-slate-300">{result.summary}</p>
            {result.eli5Summary && (
              <div className="mt-4 p-4 bg-emerald-500/10 border border-emerald-500/30 rounded-xl">
                <p className="text-sm text-emerald-400 font-medium mb-1"> Simple Explanation</p>
                <p className="text-emerald-100">{result.eli5Summary}</p>
              </div>
            )}
          </div>

          <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
            {result.marketTrends.map((trend: any, idx: number) => (
              <div key={idx} className="glass rounded-xl p-4">
                <p className="text-sm text-slate-400">{trend.trend}</p>
                <div className="flex items-center gap-2 mt-1">
                  <span className={`text-2xl font-bold ${
                    trend.direction === 'up' ? 'text-emerald-400' :
                    trend.direction === 'down' ? 'text-red-400' : 'text-slate-400'
                  }`}>
                    {trend.direction === 'up' ? '' : trend.direction === 'down' ? '' : ''}
                    {trend.percentage !== undefined ? ` ${trend.percentage}%` : ''}
                  </span>
                </div>
                {trend.explanation && <p className="text-xs text-slate-500 mt-2">{trend.explanation}</p>}
              </div>
            ))}
          </div>

          {result.keyInsights?.length > 0 && (
            <div>
              <h4 className="font-semibold text-white mb-3">Key Takeaways</h4>
              <ul className="space-y-2">
                {result.keyInsights.map((insight: string, idx: number) => (
                  <li key={idx} className="flex items-start gap-2 text-slate-300">
                    <span className="text-violet-400 mt-1"></span>
                    {insight}
                  </li>
                ))}
              </ul>
            </div>
          )}
        </div>
      )}
    </div>
  );
}

// Comps Report View Component
function CompsReportView() {
  const [formData, setFormData] = useState({
    address: '', city: '', state: '', zipCode: '',
    propertyType: 'single-family', bedrooms: '', bathrooms: '', sqft: '',
  });
  const [loading, setLoading] = useState(false);
  const [result, setResult] = useState<any>(null);
  const [error, setError] = useState<string | null>(null);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);
    setError(null);

    try {
      const { getCompsReport } = await import('../services/ai');
      const report = await getCompsReport({
        address: formData.address, city: formData.city, state: formData.state, zipCode: formData.zipCode,
        propertyType: formData.propertyType as any,
        bedrooms: formData.bedrooms ? parseInt(formData.bedrooms) : undefined,
        bathrooms: formData.bathrooms ? parseFloat(formData.bathrooms) : undefined,
        sqft: formData.sqft ? parseInt(formData.sqft) : undefined,
      });
      setResult(report);
    } catch (err) {
      setError(err instanceof Error ? err.message : 'Failed to generate comps report');
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="space-y-6">
      <div className="glass rounded-2xl p-6">
        <h2 className="text-2xl font-bold text-white mb-2">Comparative Market Analysis</h2>
        <p className="text-slate-400 mb-6">Generate property valuation analysis</p>

        <form onSubmit={handleSubmit} className="space-y-4">
          <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div className="md:col-span-2">
              <label className="block text-sm font-medium text-slate-300 mb-2">Property Address *</label>
              <input type="text" required value={formData.address}
                onChange={(e) => setFormData({ ...formData, address: e.target.value })}
                placeholder="123 Main Street"
                className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white placeholder-slate-500 focus:ring-2 focus:ring-violet-500/50"
              />
            </div>
            {['city', 'state', 'zipCode'].map((field) => (
              <div key={field}>
                <label className="block text-sm font-medium text-slate-300 mb-2 capitalize">{field === 'zipCode' ? 'ZIP Code' : field}</label>
                <input type="text" value={formData[field as keyof typeof formData]}
                  onChange={(e) => setFormData({ ...formData, [field]: e.target.value })}
                  className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white focus:ring-2 focus:ring-violet-500/50"
                />
              </div>
            ))}
            <div>
              <label className="block text-sm font-medium text-slate-300 mb-2">Property Type</label>
              <select value={formData.propertyType}
                onChange={(e) => setFormData({ ...formData, propertyType: e.target.value })}
                className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white focus:ring-2 focus:ring-violet-500/50"
              >
                {['single-family', 'condo', 'townhouse', 'multi-family', 'commercial'].map(type => (
                  <option key={type} value={type} className="bg-slate-900 capitalize">{type.replace('-', ' ')}</option>
                ))}
              </select>
            </div>
            {[
              { key: 'bedrooms', label: 'Bedrooms', type: 'number' },
              { key: 'bathrooms', label: 'Bathrooms', type: 'number', step: '0.5' },
              { key: 'sqft', label: 'Square Feet', type: 'number' },
            ].map(({ key, label, type, step }) => (
              <div key={key}>
                <label className="block text-sm font-medium text-slate-300 mb-2">{label}</label>
                <input type={type} step={step}
                  value={formData[key as keyof typeof formData]}
                  onChange={(e) => setFormData({ ...formData, [key]: e.target.value })}
                  className="w-full px-4 py-3 glass border border-white/10 rounded-xl text-white focus:ring-2 focus:ring-violet-500/50"
                />
              </div>
            ))}
          </div>

          <button type="submit" disabled={loading || !formData.address.trim()}
            className="px-6 py-3 bg-gradient-to-r from-emerald-500 to-teal-500 hover:from-emerald-400 hover:to-teal-400 disabled:opacity-50 text-white font-medium rounded-xl transition-all flex items-center gap-2"
          >
            {loading ? <><div className="w-4 h-4 border-2 border-white/30 border-t-white rounded-full animate-spin" /> Generating...</> : 'Generate Comps Report'}
          </button>
        </form>

        {error && <div className="mt-4 p-4 bg-red-500/10 border border-red-500/30 rounded-xl text-red-300">{error}</div>}
      </div>

      {result && (
        <div className="glass rounded-2xl p-6 space-y-6">
          <div className="bg-gradient-to-r from-violet-500/20 to-fuchsia-500/20 rounded-xl p-6 border border-violet-500/30">
            <h3 className="text-lg font-semibold text-violet-300 mb-2">Subject Property</h3>
            <p className="text-slate-200">{result.subjectProperty.address}</p>
            <div className="flex items-center gap-4 mt-3">
              <span className="text-3xl font-bold text-white">${result.subjectProperty.estimatedValue.toLocaleString()}</span>
              <span className={`px-3 py-1 rounded-full text-sm ${
                result.subjectProperty.confidence === 'high' ? 'bg-emerald-500/20 text-emerald-300' :
                result.subjectProperty.confidence === 'medium' ? 'bg-yellow-500/20 text-yellow-300' :
                'bg-slate-500/20 text-slate-300'
              }`}>{result.subjectProperty.confidence} confidence</span>
            </div>
          </div>

          <div>
            <h4 className="font-semibold text-white mb-4">Comparable Sales</h4>
            <div className="space-y-3">
              {result.comparables.map((comp: any, idx: number) => (
                <div key={idx} className="glass rounded-xl p-4">
                  <div className="flex justify-between items-start">
                    <div>
                      <p className="font-medium text-white">{comp.address}</p>
                      <p className="text-sm text-slate-400">{comp.bedrooms} bed  {comp.bathrooms} bath  {comp.sqft.toLocaleString()} sqft</p>
                    </div>
                    <div className="text-right">
                      <p className="font-semibold text-white">${comp.salePrice.toLocaleString()}</p>
                      <p className="text-xs text-slate-500">{comp.saleDate}</p>
                    </div>
                  </div>
                </div>
              ))}
            </div>
          </div>
        </div>
      )}
    </div>
  );
}
